/**
 * Utils.
 */
import Confirm from "./utils/Confirm";

/**
 * Directives.
 */
import ClickOutside from "./directives/ClickOutside";

/**
 * Components.
 */
import Layouts from "./utils/Layouts";
import Components from "./utils/Components";

module.exports = {
  /**
   * Install.
   *
   * @param {any} app
   */
  install(app) {
    /** Register directives **/
    app.use(ClickOutside);
    app.use(Confirm);

    /** Register layouts */
    app.use(Layouts);

    /** Register components */
    app.use(Components);
  },
};
