<template>
  <section class="p-3 sm:p-6" :class="[getSpace]">
    <div v-if="isLoading" class="flex flex-col items-center justify-center space-y-2.5 my-8">
      <svg
        class="animate-spin text-primary h-7 w-7"
        xmlns="http://www.w3.org/2000/svg"
        fill="none"
        viewBox="0 0 24 24">
        <circle
          class="opacity-25"
          cx="12"
          cy="12"
          r="10"
          stroke="currentColor"
          stroke-width="4"
        />

        <path
          class="opacity-75"
          fill="currentColor"
          d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
        />
      </svg>

      <span class="text-xs text-gray-500">Loading... Please wait</span>
    </div>

    <slot v-else />
  </section>
</template>

<script>
import { computed } from "vue";

export default {
  props: {
    /**
     * Space.
     *
     * @type {Number|Object}
     */
    space: {
      type: Number,
      default: 6,
    },

    /**
     * Is loading?
     *
     * @type {Boolean|Object}
     */
    isLoading: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props) {
    const getSpace = computed(() => {
      return "space-y-" + props.space;
    });

    return {
      getSpace,
    };
  },
};
</script>