<template>
  <transition-group
    enter-active-class="transform ease-out duration-300 transition"
    enter-from-class="translate-y-2 opacity-0 sm:translate-y-0 sm:translate-x-2"
    enter-to-class="translate-y-0 opacity-100 sm:translate-x-0"
    leave-active-class="transition ease-in duration-100"
    leave-from-class="opacity-100"
    leave-to-class="opacity-0"
  >
    <div
      v-if="data && visible"
      class="fixed top-2 right-2 z-50 bg-white shadow pointer-events-auto ring-1 ring-black ring-opacity-5 overflow-hidden md:w-11/12 md:rounded-lg lg:w-4/12"
    >
      <div class="flex">
        <div
          class="flex flex-shrink-0 justify-center items-center p-3 w-14 border-r"
          :class="[getIconBg]"
        >
          <component :is="getIcon" class="h-5 w-5" :class="[getColorType]" />
        </div>
        <div class="w-0 flex-1 p-3">
          <p class="text-sm font-semibold" :class="[getColorType]">
            {{ data.title }}
          </p>
          <p class="mt-1 text-sm text-gray-500">{{ data.message }}</p>
        </div>
        <div class="flex flex-shrink-0 justify-center items-center p-3 pr-4">
          <x-mark-icon
            @click="triggerClose"
            class="cursor-pointer w-5 h-5 text-gray-600 hover:text-gray-400"
          />
        </div>
      </div>
    </div>
  </transition-group>
</template>

<script>
import { computed, watch, ref } from "vue";
import {
  XMarkIcon,
  BellIcon,
  CheckIcon,
  ShieldExclamationIcon,
  ExclamationTriangleIcon,
  InformationCircleIcon,
} from "@heroicons/vue/24/outline";

import * as Logger from "../../helpers/Logger";

/**
 * Types.
 *
 * @type {Array}
 */
const notificationTypes = ["default", "success", "warning", "danger", "info"];

export default {
  name: "v-notification",

  components: {
    XMarkIcon,
    BellIcon,
    CheckIcon,
    ShieldExclamationIcon,
    ExclamationTriangleIcon,
    InformationCircleIcon,
  },

  props: {
    /**
     * Data.
     *
     * @type {Object}
     */
    data: {
      type: [Object, String],
      default: () => null,
    },
  },

  setup(props) {
    /**
     * Is visible?
     */
    const visible = ref(false);

    /**
     * Get type.
     *
     * @type {ComputedRef<string>}
     */
    const getType = computed(() => {
      const { data } = props;

      if (!data || !data.hasOwnProperty("type")) {
        return "default";
      }

      if (!notificationTypes.includes(data.type)) {
        console.error("Invalid notification type: " + data.type);

        return "default";
      }

      return data.type;
    });

    /**
     * Get icon.
     */
    const getIcon = computed(() => {
      const { data } = props;

      if (!data || !data.hasOwnProperty("type")) {
        return BellIcon;
      }

      switch (data.type) {
        case "success":
          return CheckIcon;
        case "warning":
          return ShieldExclamationIcon;
        case "danger":
          return ExclamationTriangleIcon;
        case "info":
          return InformationCircleIcon;
        default:
          return BellIcon;
      }
    });

    /**
     * Get color type.
     */
    const getColorType = computed(() => {
      const { data } = props;

      if (!data || !data.hasOwnProperty("type")) {
        return "text-gray-800";
      }

      switch (data.type) {
        case "success":
          return "text-green-600";
        case "warning":
          return "text-yellow-600";
        case "danger":
          return "text-red-600";
        case "info":
          return "text-blue-600";
        default:
          return "text-gray-600";
      }
    });

    /**
     * Get icon background.
     */
    const getIconBg = computed(() => {
      const { data } = props;

      if (!data || !data.hasOwnProperty("type")) {
        return "bg-gray-50";
      }

      switch (data.type) {
        case "success":
          return "bg-green-50";
        case "warning":
          return "bg-yellow-50";
        case "danger":
          return "bg-red-50";
        case "info":
          return "bg-blue-50";
        default:
          return "bg-gray-50";
      }
    });

    /**
     * Get time.
     */
    const getTime = computed(() => {
      const { data } = props;

      return data.hasOwnProperty("time") ? data.time : 5000;
    });

    /**
     * Trigger close.
     */
    const triggerClose = () => {
      visible.value = false;
    };

    /**
     * Watcher.
     */
    watch(
      () => props.data,
      (val) => {
        if (!val) return;

        Logger.log(`Notification - ${JSON.stringify(val)}`);

        visible.value = true;

        setTimeout(() => triggerClose(), getTime.value);
      }
    );

    return {
      visible,
      getType,
      getIcon,
      getColorType,
      getIconBg,
      triggerClose,
    };
  },
};
</script>
