<template>
  <th
    scope="col"
    class="px-4 py-3 text-left font-medium text-primary uppercase tracking-wider min-w"
    :style="getStyles"
  >
    <div class="inline-flex w-full items-center justify-between flex-nowrap">
      <span>{{ data.label }}</span>

      <span
        v-if="data.sortable"
        class="relative z-0 inline-flex shadow-sm rounded-md"
      >
        <button
          type="button"
          class="relative inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-100 focus:z-10 focus:outline-none"
          :class="{ 'text-blue-700': isSortBy(data.key, 'asc') }"
          @click="changeSorting(data.key, 'asc')"
        >
          <span class="sr-only">Sort ascending</span>

          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-4 w-4"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path
              d="M3 3a1 1 0 000 2h11a1 1 0 100-2H3zM3 7a1 1 0 000 2h5a1 1 0 000-2H3zM3 11a1 1 0 100 2h4a1 1 0 100-2H3zM13 16a1 1 0 102 0v-5.586l1.293 1.293a1 1 0 001.414-1.414l-3-3a1 1 0 00-1.414 0l-3 3a1 1 0 101.414 1.414L13 10.414V16z"
            />
          </svg>
        </button>

        <button
          type="button"
          class="relative inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 bg-white text-sm font-medium text-gray-500 hover:bg-gray-100 focus:z-10 focus:outline-none"
          :class="{ 'text-blue-700': isSortBy(data.key, 'desc') }"
          @click="changeSorting(data.key, 'desc')"
        >
          <span class="sr-only">Sort descending</span>

          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-4 w-4"
            viewBox="0 0 20 20"
            fill="currentColor"
          >
            <path
              d="M3 3a1 1 0 000 2h11a1 1 0 100-2H3zM3 7a1 1 0 000 2h7a1 1 0 100-2H3zM3 11a1 1 0 100 2h4a1 1 0 100-2H3zM15 8a1 1 0 10-2 0v5.586l-1.293-1.293a1 1 0 00-1.414 1.414l3 3a1 1 0 001.414 0l3-3a1 1 0 00-1.414-1.414L15 13.586V8z"
            />
          </svg>
        </button>
      </span>
    </div>
  </th>
</template>

<script>
import { computed, inject } from "vue";

export default {
  props: {
    /**
     * Name.
     *
     * @type {Object}
     */
    data: {
      type: Object,
    },
  },

  setup: function (props) {
    /**
     * Sort by.
     */
    let sortBy = inject("sortBy");

    /**
     * Get column width.
     *
     * @type {ComputedRef<null|*>}
     */
    const getStyles = computed(() => {
      let styles = [];

      // Width
      styles.push(["min-width:" + props.data.width]);

      return styles.join(";");
    });

    /**
     * Is sort by?
     *
     * @param {String} prop
     * @param {String} order
     *
     * @return {Boolean}
     */
    const isSortBy = (prop, order) => {
      return sortBy.prop === prop && sortBy.order === order;
    };

    /**
     * Change sorting.
     *
     * @param {String} prop
     * @param {String} order
     */
    const changeSorting = (prop, order) => {
      sortBy.prop = prop;
      sortBy.order = order;
    };

    return {
      sortBy,
      getStyles,
      isSortBy,
      changeSorting,
    };
  },
};
</script>
