<template>
  <div class="relative">
    <v-label
      v-if="label"
      :label="label"
      :name="name"
      :is-required="isRequired"
      :is-optional="isOptional"
    />

    <div class="my-1.5 flex rounded-md shadow-sm">
      <div class="relative flex items-stretch flex-grow">
        <div
          class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none"
        >
          <clock-icon class="w-4 h-4 text-gray-500" />
        </div>

        <input
          type="text"
          placeholder="Select time"
          class="block border text-sm pl-9 p-2 rounded-none rounded-l-md w-full pr-10 focus:outline-none"
          :class="[getErrorClass, getDisabledClass]"
          v-model="displaySelectedTime"
          :disabled="isDisabled"
          @click="showPicker"
          readonly
        />
      </div>

      <button
        class="-ml-px relative inline-flex items-center space-x-2 px-4 py-2 bg-gray-200 border text-sm font-medium rounded-r-md focus:outline-none"
        :class="[
          getErrorClass,
          getDisabledClass,
          error ? 'text-red-700' : 'text-gray-700',
        ]"
        @click="clearTime()"
      >
        <x-circle-icon class="h-4 w-4" />
      </button>
    </div>

    <error-message v-if="error">{{ error }}</error-message>

    <v-modal ref="modal" @closed="closePicker">
      <general
        v-if="mode === 'general'"
        :selected-hours="selectedHours"
        :selected-minutes="selectedMinutes"
        @setMode="setMode"
      />
      <hours
        v-else-if="mode === 'hours'"
        :selected-hours="selectedHours"
        @setMode="setMode"
        @select="selectHour"
      />
      <minutes
        v-else-if="mode === 'minutes'"
        :selected-minutes="selectedMinutes"
        :step="step"
        @setMode="setMode"
        @select="selectMinute"
      />
    </v-modal>
  </div>
</template>

<script>
import { ref, computed, watch } from "vue";
import { ClockIcon } from "@heroicons/vue/24/outline";
import { XCircleIcon } from "@heroicons/vue/20/solid";
import VLabel from "../label/Label.vue";
import VModal from "../modal/Modal.vue";

import General from "./_partials/General.vue";
import Hours from "./_partials/Hours.vue";
import Minutes from "./_partials/Minutes.vue";
import ErrorMessage from "../error-message/ErrorMessage.vue";

export default {
  components: {
    ErrorMessage,
    ClockIcon,
    General,
    Hours,
    Minutes,
    VLabel,
    VModal,
    XCircleIcon,
  },

  props: {
    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    modelValue: {
      type: String,
    },

    /**
     * Error message.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: null,
    },

    /**
     * Mode for minutes
     */
    step: {
      type: String,
      default: "fiveMinutes",
      validator: (value) => {
        return (
          [
            "fiveMinutes",
            "tenMinutes",
            "fifteenMinutes",
            "thirtyMinutes",
          ].indexOf(value) !== -1
        );
      },
    },

    /**
     * Disabled button.
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Required.
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is optional?
     *
     * @type {Boolean|Object}
     */
    isOptional: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Modal.
     *
     * @type {Ref<UnwrapRef<*>>}
     */
    const modal = ref(null);

    /**
     * Display selected time.
     *
     * @return {String}
     */
    const displaySelectedTime = computed(() => {
      return props.modelValue;
    });

    /**
     * Selected hours.
     *
     * @type {Ref<UnwrapRef<String>>}
     */
    const selectedHours = ref(null);

    /**
     * Selected minutes.
     *
     * @type {Ref<UnwrapRef<String>>}
     */
    const selectedMinutes = ref(null);

    /**
     * Current mode.
     *
     * Available modes: general, hours, minutes.
     */
    let mode = ref("general");

    /**
     * Has error.
     *
     * @return {boolean}
     */
    const hasError = computed(() => {
      return !!props.error;
    });

    /**
     * Get error class.
     */
    const getErrorClass = computed(() => {
      return hasError.value
        ? "border-red-500 focus:border-red-600"
        : "border-gray-300 focus:border-gray-400";
    });

    /**
     * Get disabled class.
     */
    const getDisabledClass = computed(() => {
      return props.isDisabled ? "bg-gray-200 text-gray-500" : null;
    });

    /**
     * Set mode.
     *
     * @param {String} newMode
     */
    const setMode = (newMode) => {
      mode.value = newMode;
    };

    /**
     * Show picker.
     */
    const showPicker = () => {
      const time = props.modelValue
        ? props.modelValue.split(":")
        : ["00", "00"];

      selectedHours.value = time[0];
      selectedMinutes.value = time[1];

      modal.value.open();
    };

    /**
     * Close picker.
     */
    const closePicker = () => {
      if (selectedHours.value && selectedMinutes.value) {
        emit(
          "update:modelValue",
          selectedHours.value + ":" + selectedMinutes.value
        );
      }
    };

    /**
     * Clear time.
     */
    const clearTime = () => {
      selectedHours.value = null;
      selectedMinutes.value = null;

      emit("update:modelValue", null);
    };

    /**
     * Select hour.
     *
     * @param {String} newHour
     */
    const selectHour = (newHour) => {
      selectedHours.value = newHour;
    };

    /**
     * Select minute.
     *
     * @param {String} newMinutes
     */
    const selectMinute = (newMinutes) => {
      selectedMinutes.value = newMinutes;
    };

    /**
     * Watcher.
     */
    watch(
      () => props.modelValue,
      (value) => {
        console.log(value);
      }
    );

    return {
      modal,
      mode,
      getErrorClass,
      getDisabledClass,
      displaySelectedTime,
      selectedHours,
      selectedMinutes,
      setMode,
      showPicker,
      closePicker,
      clearTime,
      selectHour,
      selectMinute,
    };
  },
};
</script>
