<template>
  <div class="flex justify-between">
    <label :for="name" class="block text-sm font-medium text-gray-700">
      {{ label }}
      <span v-if="isRequired" class="text-xs text-red-700">*</span>
    </label>

    <span v-if="isOptional" class="text-xs text-gray-500">Optional</span>
  </div>
</template>

<script>
export default {
  props: {
    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Is Required?
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is optional?
     *
     * @type {Boolean|Object}
     */
    isOptional: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },
};
</script>
