<template>
  <Menu as="div" class="relative inline-block text-left">
    <MenuButton
      class="inline-flex items-center justify-center border py-1.5 rounded shadow"
      :class="[getType, getSize]"
    >
      {{ label }}
      <chevron-down-icon class="-mr-1 ml-3 h-4 w-4" />
    </MenuButton>

    <transition
      enter-active-class="transition duration-100 ease-out"
      enter-from-class="transform scale-95 opacity-0"
      enter-to-class="transform scale-100 opacity-100"
      leave-active-class="transition duration-75 ease-in"
      leave-from-class="transform scale-100 opacity-100"
      leave-to-class="transform scale-95 opacity-0"
    >
      <MenuItems
        class="absolute w-56 mt-2 bg-white divide-y divide-gray-100 rounded shadow-lg focus:outline-none z-40"
        :class="[
          direction === 'right'
            ? 'left-0 origin-top-left'
            : 'right-0 origin-top-right',
        ]"
      >
        <div class="p-1">
          <MenuItem v-for="(item, index) in items" :key="index">
            <button
              class="group flex rounded-md items-center w-full px-2 py-2 hover:bg-gray-100"
              :class="[getItemSize]"
              @click="click(item)"
            >
              <component
                v-if="item.icon !== undefined"
                :is="item.icon"
                class="w-4 h-4 text-gray-500 mr-2"
              />
              <span class="text-gray-800">{{ item.label }}</span>
            </button>
          </MenuItem>
        </div>
      </MenuItems>
    </transition>
  </Menu>
</template>

<script>
import { Menu, MenuButton, MenuItems, MenuItem } from "@headlessui/vue";
import { ref, computed } from "vue";
import { ChevronDownIcon } from "@heroicons/vue/24/outline";

export default {
  components: {
    ChevronDownIcon,
    Menu,
    MenuButton,
    MenuItems,
    MenuItem,
  },

  props: {
    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * Items.
     *
     * @type {Array|Object}
     */
    items: {
      type: Array,
    },

    /**
     * Direction of dropdown list.
     *
     * @type {String|Object}
     */
    direction: {
      type: String,
      default: "right",
      validator: (value) => {
        return ["right", "left"].indexOf(value) !== -1;
      },
    },

    /**
     * Size.
     *
     * @type {String|Object}
     */
    size: {
      type: String,
      default: "normal",
      validator: (value) => {
        return ["small", "normal", "large"].indexOf(value) !== -1;
      },
    },

    /**
     * Is loading?
     *
     * @type {Boolean|Object}
     */
    isLoading: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is disabled?
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props) {
    /**
     * Click.
     *
     * @param {Object} item
     */
    const click = (item) => {
      if (item.hasOwnProperty("click")) {
        item.click();
      }
    };

    /**
     * Get type.
     *
     * @return {String}
     */
    const getType = computed(() => {
      const isBusy = props.isDisabled || props.isLoading;

      return isBusy
        ? "bg-gray-300 border-transparent text-white cursor-not-allowed"
        : "bg-gray-700 border-transparent text-white hover:bg-gray-900";
    });

    /**
     * Get size.
     *
     * @return {String}
     */
    const getSize = computed(() => {
      switch (props.size) {
        case "small":
          return "px-3 text-xs";
        case "large":
          return "px-4 text-base";
        default:
          return "px-3 text-sm";
      }
    });

    /**
     * Get item size.
     *
     * @return {String}
     */
    const getItemSize = computed(() => {
      switch (props.size) {
        case "small":
          return "text-xs";
        case "large":
          return "text-base";
        default:
          return "text-sm";
      }
    });

    return {
      getSize,
      getItemSize,
      getType,
      click,
    };
  },
};
</script>
