<template>
  <div v-if="isShow"
       class="fixed z-10 inset-0 overflow-y-auto"
       role="dialog"
       aria-modal="true">
    <div class="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0"
         @click="cancel()">
      <div class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>
      <span class="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>

      <div
        class="inline-block align-middle bg-white rounded px-4 pt-5 pb-4 text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:max-w-lg sm:w-full sm:p-6">
        <div class="sm:flex sm:items-start">
          <div
            class="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-blue-100 sm:mx-0 sm:h-10 sm:w-10">
            <svg class="h-6 w-6 text-gray-700"
                 xmlns="http://www.w3.org/2000/svg"
                 fill="none"
                 viewBox="0 0 24 24"
                 stroke="currentColor"
                 aria-hidden="true">
              <path stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-width="2"
                    d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z" />
            </svg>
          </div>

          <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
            <h3 class="text-lg leading-6 font-medium text-gray-700">{{ dialog.title }}</h3>

            <div class="mt-2">
              <p class="text-sm text-gray-500">{{ dialog.message }}</p>
            </div>
          </div>
        </div>

        <div class="mt-6 flex flex-row justify-center space-x-2 sm:justify-end">
          <v-button class="w-28"
                    size="small"
                    @click="confirm()">
            <svg xmlns="http://www.w3.org/2000/svg"
                 class="h-4 w-4 mr-2"
                 viewBox="0 0 20 20"
                 fill="currentColor">
              <path fill-rule="evenodd"
                    clip-rule="evenodd"
                    d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" />
            </svg>

            {{ dialog.confirmText }}
          </v-button>

          <v-button class="w-28"
                    type="secondary"
                    size="small"
                    @click="cancel()">
            <svg xmlns="http://www.w3.org/2000/svg"
                 class="h-4 w-4 mr-2"
                 viewBox="0 0 20 20"
                 fill="currentColor">
              <path fill-rule="evenodd"
                    clip-rule="evenodd"
                    d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" />
            </svg>

            {{ dialog.cancelText }}
          </v-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, onBeforeUnmount } from "vue";
import { emitter } from "../../helpers/Emitter";
import cloneDeep from "lodash.clonedeep";

/**
 * Init state.
 *
 * @type {Object}
 */
const initState = {
  title: "Are you sure?",
  message: "",
  confirmText: "Confirm",
  cancelText: "Cancel",
};

export default {
  name: "v-confirm-box",

  setup() {
    /**
     * Is show box?
     *
     * @type {ToRef<Boolean>}
     */
    let isShow = ref(false);

    /**
     * Dialog.
     *
     * @type {ToRef<Object>}
     */
    let dialog = ref(cloneDeep(initState));

    /**
     * Open box.
     *
     * @param {Object} params
     */
    const open = (params) => {
      dialog.value = {
        ...dialog.value,
        ...params,
      };
      isShow.value = true;
    };

    /**
     * Close.
     */
    const close = () => {
      dialog.value = cloneDeep(initState);
      isShow.value = false;
    };

    /**
     * Confirm.
     */
    const confirm = () => {
      if (dialog.value.callback) {
        dialog.value.callback(true);
      }

      close();
    };

    /**
     * Cancel.
     */
    const cancel = () => {
      if (dialog.value.callback) {
        dialog.value.callback(false);
      }

      close();
    };

    /**
     * Key up.
     *
     * @param {Number} keyCode
     */
    const keyUp = ({ keyCode }) => {
      if (keyCode !== 27) return;

      cancel();
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      if (!document) return;

      emitter.on("confirmOpen", open);
      emitter.on("confirmClose", cancel);

      document.addEventListener("keyup", keyUp);
    });

    /**
     * On before unmount.
     */
    onBeforeUnmount(() => {
      document.removeEventListener("keyup", keyUp);
    });

    return {
      isShow,
      dialog,
      open,
      close,
      cancel,
      confirm,
    };
  },
};
</script>
