<template>
  <Menu as="div" class="relative inline-block text-left">
    <div>
      <MenuButton
        class="bg-white hover:bg-gray-200 max-w-xs rounded flex items-center text-sm p-2 rounded-md"
      >
        <span class="block ml-3 text-gray-700 text-sm font-medium">{{
          userName
        }}</span>
        <chevron-down-icon
          class="block flex-shrink-0 ml-1 h-5 w-5 text-gray-400"
        />
      </MenuButton>
    </div>

    <transition
      enter-active-class="transition ease-out duration-100"
      enter-from-class="transform opacity-0 scale-95"
      enter-to-class="transform opacity-100 scale-100"
      leave-active-class="transition ease-in duration-75"
      leave-from-class="transform opacity-100 scale-100"
      leave-to-class="transform opacity-0 scale-95"
    >
      <MenuItems
        class="origin-top-right absolute right-0 mt-2 w-48 rounded-md shadow-lg py-1 bg-white z-20"
      >
        <div class="py-1 divide-y divide-gray-100">
          <MenuItem
            v-for="(item, index) in actions"
            v-slot="{ active }"
            :key="index"
          >
            <div
              :class="[
                active ? 'bg-gray-50' : 'bg-white',
                'w-full block cursor-pointer px-4 py-2 text-sm text-gray-700',
              ]"
              @click="item.onClick"
            >
              {{ item.label }}
            </div>
          </MenuItem>

          <MenuItem>
            <div
              class="w-full block cursor-pointer px-4 py-2 text-sm text-gray-700 hover:bg-gray-50"
              @click="logout"
            >
              Logout
            </div>
          </MenuItem>
        </div>
      </MenuItems>
    </transition>
  </Menu>
</template>

<script>
import { Menu, MenuButton, MenuItem, MenuItems } from "@headlessui/vue";
import { ChevronDownIcon } from "@heroicons/vue/20/solid";

export default {
  components: {
    ChevronDownIcon,
    Menu,
    MenuButton,
    MenuItem,
    MenuItems,
  },

  props: {
    /**
     * User name.
     */
    userName: {
      type: String,
    },

    /**
     * Actions.
     *
     * @type {Array|Object}
     */
    actions: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },

  /**
   * Setup.
   *
   * @return {Object}
   */
  setup(props, { emit }) {
    /**
     * Logout process.
     */
    const logout = () => emit("logout");

    return {
      logout,
    };
  },
};
</script>
