<template>
  <Dialog :open="isShow" as="div" @close="close">
    <div class="fixed inset-0 z-10 overflow-y-auto">
      <div class="min-h-screen px-4 text-center">
        <DialogOverlay class="fixed inset-0 bg-black opacity-40" />

        <span class="inline-block h-screen align-middle" aria-hidden="true"
          >&#8203;</span
        >

        <transition
          enter="duration-300 ease-out"
          enter-from="opacity-0 scale-95"
          enter-to="opacity-100 scale-100"
          leave="duration-200 ease-in"
          leave-from="opacity-100 scale-100"
          leave-to="opacity-0 scale-95"
        >
          <div
            class="inline-block w-full max-w-md p-4 overflow-hidden text-left align-middle transition-all transform bg-white shadow-xl rounded"
          >
            <slot />

            <div
              v-if="$slots.actions"
              class="mt-5 sm:mt-6 sm:grid sm:col-auto sm:gap-3 sm:grid-flow-row-dense"
            >
              <slot name="actions" />
            </div>
          </div>
        </transition>
      </div>
    </div>
  </Dialog>
</template>

<script>
import { ref } from "vue";
import { Dialog, DialogOverlay, DialogTitle } from "@headlessui/vue";

export default {
  components: {
    Dialog,
    DialogOverlay,
    DialogTitle,
  },

  setup(props, { emit }) {
    /**
     * Is show?
     *
     * @type {ToRef<boolean>}
     */
    let isShow = ref(false);

    /**
     * Open modal.
     */
    const open = () => {
      isShow.value = true;
      emit("opened");
    };

    /**
     * Close modal.
     */
    const close = () => {
      isShow.value = false;
      emit("closed");
    };

    return {
      isShow,
      open,
      close,
    };
  },
};
</script>
