<template>
  <span class="text-center rounded border px-2 py-1" :class="[getType, getSize]"><slot /></span>
</template>

<script>
import { computed } from "vue";

/**
 * Types.
 */
const TYPE_DEFAULT = "default";
const TYPE_DANGER = "danger";
const TYPE_SUCCESS = "success";
const TYPE_INFO = "info";
const TYPE_WARNING = "warning";

/**
 * Sizes
 */
const SIZE_SMALL = "small";
const SIZE_NORMAL = "normal";
const SIZE_LARGE = "large";

export default {
  props: {
    /**
     * Type.
     *
     * @type {String|Object}
     */
    type: {
      type: String,
      default: TYPE_DEFAULT,
      validator: (value) => {
        return (
          [TYPE_DEFAULT, TYPE_DANGER, TYPE_SUCCESS, TYPE_INFO, TYPE_WARNING].indexOf(value) !== -1
        );
      },
    },

    /**
     * Size.
     *
     * @type {String|Object}
     */
    size: {
      type: String,
      default: SIZE_NORMAL,
      validator: (value) => {
        return [SIZE_SMALL, SIZE_NORMAL, SIZE_LARGE].indexOf(value) !== -1;
      },
    },
  },

  setup(props) {
    /**
     * Get type.
     *
     * @return {String}
     */
    const getType = computed(() => {
      switch (props.type) {
        case TYPE_DANGER:
          return "border-red-500 text-red-500 bg-red-100";
        case TYPE_SUCCESS:
          return "border-green-500 text-green-500 bg-green-100";
        case TYPE_INFO:
          return "border-blue-500 text-blue-500 bg-blue-100";
        case TYPE_WARNING:
          return "border-yellow-500 text-yellow-500 bg-yellow-100";
        default:
          return "border-gray-500 text-gray-500 bg-gray-100";
      }
    });
    /**
     * Get size.
     *
     * @return {String}
     */
    const getSize = computed(() => {
      switch (props.size) {
        case SIZE_SMALL:
          return "text-xs";
        case SIZE_LARGE:
          return "text-base";
        default:
          return "text-sm";
      }
    });

    return {
      getType,
      getSize,
    };
  },
};
</script>
