<template>
  <section>
    <div class="flex justify-between items-center mb-2">
      <div>
        <span class="text-md font-bold text-gray-800 cursor-pointer">
          {{ MONTH_NAMES[month] }}
        </span>

        <span class="ml-2 text-md text-gray-600 font-normal cursor-pointer" @click="goToYear()">
          {{ year }}
        </span>
      </div>
      <div>
        <button
          type="button"
          @click="prevMonth()">
          <svg class="h-6 w-6 text-gray-500 inline-flex" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
        </button>

        <button
          type="button"
          @click="nextMonth()">
          <svg class="h-6 w-6 text-gray-500 inline-flex" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>
    </div>

    <div class="grid grid-cols-7 mb-3 -mx-1">
      <div v-for="(day, index) in DAYS" :key="index">
        <div style="width: 14.26%" class="px-1">
          <div class="text-gray-700 font-medium text-center text-xs">{{ day }}</div>
        </div>
      </div>
    </div>

    <div class="grid grid-cols-7 -mx-1">
      <div v-for="blankday in blankDays">
        <div class="text-center border p-1 border-transparent text-sm"></div>
      </div>

      <div v-for="(day, dayIndex) in allDays" :key="dayIndex">
        <div class="px-1 mb-1">
          <div
            :class="[getDayClasses(day), 'cursor-pointer text-center text-sm leading-none rounded-full leading-loose duration-100']"
            @click="selectDate(day)">
            {{ day }}
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { onMounted, ref } from "vue";
import moment from "moment";

export default {
  name: "v-date-picker-month",

  props: {
    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    modelValue: {
      type: Object,
    },

    /**
     * Date format.
     */
    dateFormat: {
      type: String,
    },

    /**
     * Minimum date.
     *
     * @type {String|Object}
     */
    minDate: {
      type: String,
    },

    /**
     * Maxiumum date.
     *
     * @type {String|Object}
     */
    maxDate: {
      type: String,
    },
  },

  setup(props, { emit }) {
    /**
     * Select date.
     *
     * @type {moment}
     */
    const selectedDate = ref(null);

    /**
     * Day.
     *
     * @type {Number}
     */
    const day = ref(null);

    /**
     * Month.
     *
     * @type {Number}
     */
    const month = ref(null);

    /**
     * Year.
     *
     * @type {Number}
     */
    const year = ref(null);

    /**
     * Month names.
     *
     * @type {Array}
     */
    const MONTH_NAMES = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    /**
     * Days of week.
     *
     * @type {Array}
     */
    const DAYS = [
      "Mon",
      "Tue",
      "Wed",
      "Thu",
      "Fri",
      "Sat",
      "Sun",
    ];

    /**
     * Blank days.
     *
     * @type {Ref<UnwrapRef<Array>>}
     */
    const blankDays = ref([]);

    /**
     * All month days.
     *
     * @type {Ref<UnwrapRef<Array>>}
     */
    const allDays = ref([]);

    /**
     * Get month days.
     */
    const getMonthDays = () => {
      let daysInMonth = moment().year(year.value).month(month.value).daysInMonth();
      let dayOfWeek = new Date(year.value, month.value).getDay() - 1;

      let blankdaysArray = [];
      for (let i = 1; i <= dayOfWeek; i++) {
        blankdaysArray.push(i);
      }

      let daysArray = [];
      for (let i = 1; i <= daysInMonth; i++) {
        daysArray.push(i);
      }

      blankDays.value = blankdaysArray;
      allDays.value = daysArray;
    };

    /**
     * Go to the prev month.
     */
    const prevMonth = () => {
      if (month.value === 0) {
        year.value--;
        month.value = 11;
      } else {
        month.value--;
      }

      getMonthDays();
    };

    /**
     * Go to the next month.
     */
    const nextMonth = () => {
      if (month.value === 11) {
        year.value++;
        month.value = 0;
      } else {
        month.value++;
      }

      getMonthDays();
    };

    /**
     * Is today?
     *
     * @return {Boolean}
     */
    const isToday = (currentDate) => {
      return moment().isSame(currentDate);
    };

    /**
     * Is selected?
     */
    const isSelected = (currentDate) => {
      return selectedDate.value.isSame(currentDate);
    };

    /**
     * Is before.
     */
    const isBefore = (currentDate) => {
      if (!props.minDate) {
        return false;
      }

      let mDate = moment(props.minDate, props.dateFormat);

      return currentDate.isBefore(mDate);
    };

    /**
     * Is after.
     */
    const isAfter = (currentDate) => {
      if (!props.maxDate) {
        return false;
      }

      let mDate = moment(props.maxDate, props.dateFormat);

      return currentDate.isAfter(mDate);
    };

    /**
     * Get day classes.
     *
     * @param {Number} day
     *
     * @return {String}
     */
    const getDayClasses = (day) => {
      let currentDate = moment(`${year.value}-${month.value + 1}-${day}`, "YYYY-M-D");

      if (isBefore(currentDate) || isAfter(currentDate)) {
        return "text-gray-300";
      }

      if (isSelected(currentDate)) {
        return "bg-gray-600 text-white";
      }

      if (isToday(currentDate)) {
        return "bg-gray-300 text-white";
      }

      return "text-gray-700 hover:bg-blue-200";
    };

    /**
     * Set day.
     *
     * @param {Number} day
     */
    const selectDate = (day) => {
      let currentDate = moment(`${year.value}-${month.value + 1}-${day}`, "YYYY-M-D");

      if (isAfter(currentDate) || isBefore(currentDate)) {
        return;
      }

      selectedDate.value = currentDate;
      emit("update:modelValue", currentDate);
      emit("selected");
    };

    /**
     * Go to year mode.
     */
    const goToYear = () => {
      emit("mode", "years");
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      selectedDate.value = props.modelValue ?? moment();

      month.value = selectedDate.value.get("month");
      year.value = selectedDate.value.get("year");
      day.value = selectedDate.value.get("date");

      getMonthDays();
    });

    return {
      MONTH_NAMES,
      DAYS,
      blankDays,
      allDays,
      month,
      year,
      getDayClasses,
      prevMonth,
      nextMonth,
      selectDate,
      goToYear,
    };
  },
};
</script>
