<template>
  <button
    type="submit"
    class="inline-flex items-center justify-center border py-1.5 rounded shadow"
    :style="customStyle"
    :class="[getType, getSize]"
    :disabled="isDisabled || isLoading">
    <svg
      v-if="isLoading"
      class="animate-spin h-4 w-4"
      :class="[getIconSize]"
      xmlns="http://www.w3.org/2000/svg"
      fill="none"
      viewBox="0 0 24 24">
      <circle
        class="opacity-25"
        cx="12"
        cy="12"
        r="10"
        stroke="currentColor"
        stroke-width="4"
      />

      <path
        class="opacity-75"
        fill="currentColor"
        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
      />
    </svg>

    <slot v-else />
  </button>
</template>

<script>
import { computed } from "vue";

export default {
  props: {
    /**
     * Type.
     *
     * @type {String|Object}
     */
    type: {
      type: String,
      default: "default",
      validator: (value) => {
        return (
          ["default", "danger", "success", "info", "warning", "secondary", "custom"].indexOf(
            value,
          ) !== -1
        );
      },
    },

    /**
     * Size.
     *
     * @type {String|Object}
     */
    size: {
      type: String,
      default: "normal",
      validator: (value) => {
        return ["small", "normal", "large"].indexOf(value) !== -1;
      },
    },

    /**
     * Custom style.
     *
     * @type {String|Object}
     */
    customStyle: {
      type: String,
      default: null,
    },

    /**
     * Is loading?
     *
     * @type {Boolean|Object}
     */
    isLoading: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is disabled?
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props) {
    /**
     * Get type.
     *
     * @return {String}
     */
    const getType = computed(() => {
      const isBusy = props.isDisabled || props.isLoading;

      switch (props.type) {
        case "success":
          return isBusy
            ? "bg-green-300 border-transparent text-white cursor-not-allowed"
            : "bg-green-500 border-transparent text-white hover:bg-green-700";
        case "danger":
          return isBusy
            ? "bg-red-300 border-transparent text-white cursor-not-allowed"
            : "bg-red-500 border-transparent text-white hover:bg-red-700";
        case "warning":
          return isBusy
            ? "bg-yellow-200 border-transparent text-white cursor-not-allowed"
            : "bg-yellow-500 border-transparent text-white hover:bg-yellow-700";
        case "info":
          return isBusy
            ? "bg-blue-300 border-transparent text-white cursor-not-allowed"
            : "bg-blue-500 border-transparent text-white hover:bg-blue-700";
        case "secondary":
          return isBusy
            ? "bg-white border-gray-500 text-gray-500 cursor-not-allowed"
            : "bg-white border-gray-600 text-gray-600 hover:bg-gray-100";
        case "custom":
          return isBusy
            ? "cursor-not-allowed"
            : null;
        default:
          return isBusy
            ? "bg-gray-300 border-transparent text-white cursor-not-allowed"
            : "bg-gray-700 border-transparent text-white hover:bg-gray-900";
      }
    });

    /**
     * Get size.
     *
     * @return {String}
     */
    const getSize = computed(() => {
      switch (props.size) {
        case "small":
          return "px-3 text-xs";
        case "large":
          return "px-4 text-base";
        default:
          return "px-3 text-sm";
      }
    });

    /**
     * Get icon size.
     *
     * @return {String}
     */
    const getIconSize = computed(() => {
      switch (props.size) {
        case "small":
          return "my-0";
        case "large":
          return "my-1";
        default:
          return "my-0.5";
      }
    });

    return {
      getType,
      getSize,
      getIconSize,
    };
  },
};
</script>
