<template>
  <div>
    <div class="text-center p-3">
      <span class="text-md font-bold text-gray-800">Hours</span>
    </div>

    <div class="p-3">
      <div class="grid grid-cols-6 gap-2">
        <div v-for="(hour, index) in optionsHours" :key="index">
          <div class="flex items-center justify-center">
            <div
              :class="[getHourClasses(hour), 'flex w-full items-center justify-center px-5 py-3 cursor-pointer text-sm rounded duration-200']"
              @click="selectHour(hour)">
              {{ hour }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import range from "lodash.range";

export default {
  props: {
    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    selectedHours: {
      type: String,
    },
  },

  setup(props, { emit }) {
    /**
     * Options of hours.
     *
     * @type {Array}
     */
    const optionsHours = range(0, 24).map(digit => {
      if (digit < 10) return "0" + digit;

      return digit.toString();
    });

    /**
     * Is selected?
     *
     * @param {String} hour
     *
     * @return {Boolean}
     */
    const isSelected = (hour) => {
      return props.selectedHours === hour;
    };

    /**
     * Get hour classes.
     *
     * @param {String} hour
     *
     * @return {String}
     */
    const getHourClasses = (hour) => {
      if (isSelected(hour)) {
        return "bg-gray-600 text-white";
      }

      return "text-gray-700 hover:bg-primary hover:text-white";
    };

    /**
     * Select hour.
     *
     * @param {String} hour
     */
    const selectHour = (hour) => {
      emit("select", hour);
      emit("setMode", "general");
    };

    return {
      optionsHours,
      getHourClasses,
      selectHour,
    };
  },
};
</script>
