/**
 * Components: form
 */
import VAmount from "../components/amount/Amount.vue";
import VButton from "../components/button/Button.vue";
import VCheckbox from "../components/checkbox/Checkbox.vue";
import VDatePicker from "../components/datepicker/DatePicker.vue";
import VForm from "../components/form/Form.vue";
import VLabel from "../components/label/Label.vue";
import VSelect from "../components/select/Select.vue";
import VSlideOver from "../components/slide-over/SlideOver.vue";
import VText from "../components/text/Text.vue";
import VTextarea from "../components/textarea/Textarea.vue";
import VTimePicker from "../components/timepicker/TimePicker.vue";

/**
 * Components: layout
 */
import VAlert from "../components/alert/Alert.vue";
import VCard from "../components/card/Card.vue";
import VDropdown from "../components/dropdown/Dropdown.vue";
import VFiles from "../components/files/Files.vue"; // TODO: Add to storybook
import VGrid from "../components/grid/Grid.vue";
import VHeader from "../components/header/Header.vue";
import VMobileSidebar from "../components/mobile-sidebar/MobileSidebar.vue"; // TODO: Add to storybook
import VModal from "../components/modal/Modal.vue";
import VNav from "../components/nav/Nav.vue";
import VNotification from "../components/notification/Notification.vue";
import VPreview from "../components/preview/Preview.vue"; // TODO: Add to storybook
import VPreviewItem from "../components/preview-item/PreviewItem.vue"; // TODO: Add to storybook
import VProfileDropdown from "../components/profile-dropdown/ProfileDropdown.vue";
import VSection from "../components/section/Section.vue"; // TODO: Add to storybook
import VSidebar from "../components/sidebar/Sidebar.vue"; // TODO: Add to storybook
import VTable from "../components/table/Table.vue";
import VTag from "../components/tag/Tag.vue"; // TODO: Add to storybook
import VUploadFiles from "../components/upload-files/UploadFiles.vue"; // TODO: Add to storybook
import VNumber from "../components/number/Number.vue";

/**
 * Export.
 */
export default {
  /**
   * Install plugin.
   *
   * @param {any} app
   */
  install(app) {
    /** Register components **/
    app.component("v-alert", VAlert);
    app.component("v-amount", VAmount);
    app.component("v-button", VButton);
    app.component("v-card", VCard);
    app.component("v-checkbox", VCheckbox);
    app.component("v-date-picker", VDatePicker);
    app.component("v-dropdown", VDropdown);
    app.component("v-files", VFiles);
    app.component("v-form", VForm);
    app.component("v-grid", VGrid);
    app.component("v-header", VHeader);
    app.component("v-label", VLabel);
    app.component("v-mobile-sidebar", VMobileSidebar);
    app.component("v-modal", VModal);
    app.component("v-nav", VNav);
    app.component("v-notification", VNotification);
    app.component("v-preview", VPreview);
    app.component("v-preview-item", VPreviewItem);
    app.component("v-profile-dropdown", VProfileDropdown);
    app.component("v-section", VSection);
    app.component("v-select", VSelect);
    app.component("v-slide-over", VSlideOver);
    app.component("v-sidebar", VSidebar);
    app.component("v-table", VTable);
    app.component("v-tag", VTag);
    app.component("v-text", VText);
    app.component("v-textarea", VTextarea);
    app.component("v-time-picker", VTimePicker);
    app.component("v-upload-files", VUploadFiles);
    app.component("v-number", VNumber);
  },
};
