<template>
  <nav
    class="bg-white py-2 flex-row sm:flex items-center justify-between border-t border-gray-300 sm:px-1 sm:py-3"
    aria-label="Pagination"
  >
    <div class="px-2 hidden pb-3 sm:block sm:pb-0 sm:text-left text-xs">
      Showing
      <span class="font-medium">{{ pagination.from }}</span>
      to
      <span class="font-medium">{{ pagination.to }}</span>
      of
      <span class="font-medium">{{ totalItems }}</span>
      results
    </div>

    <div class="px-2 flex-1 flex justify-between sm:justify-end">
      <v-button
        class="w-20 inline-flex items-center"
        :is-disabled="currentPage === 1"
        :is-loading="isLoading"
        @click="goPrev()"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-4 w-4"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M15 19l-7-7 7-7"
          />
        </svg>
      </v-button>

      <v-button
        class="w-20 inline-flex items-center sm:ml-2"
        :is-disabled="currentPage === lastPage"
        :is-loading="isLoading"
        @click="goNext()"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-4 w-4"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M9 5l7 7-7 7"
          />
        </svg>
      </v-button>
    </div>
  </nav>
</template>

<script>
import { inject } from "vue";

import VButton from "../../button/Button.vue";

export default {
  components: {
    VButton,
  },

  props: {
    /**
     * Current page.
     *
     * @type {Number|Object}
     */
    currentPage: {
      type: Number,
    },

    /**
     * Last page.
     *
     * @type {Number|Object}
     */
    lastPage: {
      type: Number,
    },

    /**
     * Total items.
     *
     * @type {Number|Object}
     */
    totalItems: {
      type: Number,
    },

    /**
     * Pagination.
     *
     * @type {Object}
     */
    pagination: {
      type: Object,
      default: () => {
        return {
          from: 0,
          to: 0,
        };
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Sort by.
     */
    let isLoading = inject("isLoading");

    /**
     * Go prev.
     */
    const goPrev = () => emit("prev");

    /**
     * Go next.
     */
    const goNext = () => emit("next");

    return {
      isLoading,
      goPrev,
      goNext,
    };
  },
};
</script>
