<template>
  <div class="border-l-2 rounded" :class="[getType, { shadow: shadow }]">
    <div class="flex">
      <div
        class="w-10 py-3 flex-none flex items-center justify-center"
        :class="[getIconBg]"
      >
        <component :is="getIcon" class="h-5 w-5" aria-hidden="true" />
      </div>
      <div class="py-3 px-2 flex-grow flex items-center">
        <span class="text-sm"><slot /></span>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import {
  CheckIcon,
  XMarkIcon,
  ExclamationTriangleIcon,
  InformationCircleIcon,
} from "@heroicons/vue/24/outline";

export default {
  components: {
    CheckIcon,
    ExclamationTriangleIcon,
    InformationCircleIcon,
    XMarkIcon,
  },

  props: {
    /**
     * Type.
     *
     * @type {String|Object}
     */
    type: {
      type: String,
      validator: (value) => {
        return ["danger", "success", "info", "warning"].indexOf(value) !== -1;
      },
    },

    /**
     * Shadow.
     *
     * @type {Boolean|Object}
     */
    shadow: {
      type: Boolean,
      default: () => false,
    },
  },

  setup(props) {
    /**
     * Get type.
     *
     * @return {String}
     */
    const getType = computed(() => {
      switch (props.type) {
        case "success":
          return "bg-green-100 border-green-400 text-green-700";
        case "danger":
          return "bg-red-100 border-red-400 text-red-700";
        case "warning":
          return "bg-yellow-100 border-yellow-400 text-yellow-700";
        case "info":
          return "bg-blue-100 border-blue-400 text-blue-700";
      }
    });

    /**
     * Get icon.
     */
    const getIcon = computed(() => {
      switch (props.type) {
        case "success":
          return CheckIcon;
        case "danger":
          return XMarkIcon;
        case "warning":
          return ExclamationTriangleIcon;
        case "info":
          return InformationCircleIcon;
      }
    });

    /**
     * Get icon background.
     */
    const getIconBg = computed(() => {
      switch (props.type) {
        case "success":
          return "bg-green-50";
        case "danger":
          return "bg-red-50";
        case "warning":
          return "bg-yellow-50";
        case "info":
          return "bg-blue-50";
      }
    });

    return {
      getType,
      getIcon,
      getIconBg,
    };
  },
};
</script>
