<template>
  <div>
    <div class="text-center p-3">
      <span class="text-md font-bold text-gray-800">Minutes</span>
    </div>

    <div class="p-3">
      <div class="grid gap-2" :class="[getColsClasses]">
        <div v-for="(minute, index) in optionsMinutes" :key="index">
          <div class="flex items-center justify-center">
            <div
              :class="[getHourClasses(minute), 'flex w-full items-center justify-center px-5 py-3 cursor-pointer text-sm rounded duration-200']"
              @click="selectHour(minute)">
              {{ minute }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import range from "lodash.range";

export default {
  props: {
    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    selectedMinutes: {
      type: String,
    },

    /**
     * Mode of minutes.
     */
    step: {
      type: String,
    },
  },

  setup(props, { emit }) {
    /**
     * Options of minutes.
     *
     * @type {Array}
     */
    const optionsMinutes = computed(() => {
      let rangeArr = [];

      if (props.step === "fiveMinutes") {
        rangeArr = range(0, 60, 5);
      } else if (props.step === "tenMinutes") {
        rangeArr = range(0, 60, 10);
      } else if (props.step === "fifteenMinutes") {
        rangeArr = range(0, 60, 15);
      } else if (props.step === "thirtyMinutes") {
        rangeArr = range(0, 60, 30);
      }

      return rangeArr.map(digit => {
        if (digit < 10) return "0" + digit;

        return digit.toString();
      });
    });

    /**
     * Get cols classes.
     *
     * @return {String}
     */
    const getColsClasses = computed(() => {
      if (props.step === "fiveMinutes") {
        return "grid-cols-4";
      } else if (props.step === "tenMinutes") {
        return "grid-cols-3";
      } else if (props.step === "fifteenMinutes") {
        return "grid-cols-4";
      } else if (props.step === "thirtyMinutes") {
        return "grid-cols-2";
      }
    });

    /**
     * Is selected?
     *
     * @param {String} minute
     *
     * @return {Boolean}
     */
    const isSelected = (minute) => {
      return props.selectedMinutes === minute;
    };

    /**
     * Get minute classes.
     *
     * @param {String} minute
     *
     * @return {String}
     */
    const getHourClasses = (minute) => {
      if (isSelected(minute)) {
        return "bg-gray-600 text-white";
      }

      return "text-gray-700 hover:bg-primary hover:text-white";
    };

    /**
     * Select minute.
     *
     * @param {String} minute
     */
    const selectHour = (minute) => {
      emit("select", minute);
      emit("setMode", "general");
    };

    return {
      optionsMinutes,
      getColsClasses,
      getHourClasses,
      selectHour,
    };
  },
};
</script>
