import { emitter } from "../helpers/Emitter";
import ConfirmBox from "../components/confirm-box/ConfirmBox.vue";

/**
 * ConfirmClass
 */
class ConfirmClass {
  /**
   * Show confirm box.
   *
   * @param {Object} params
   */
  show(params = {}) {
    if (typeof params !== "object" || Array.isArray(params)) {
      let caughtType = typeof params;

      if (Array.isArray(params)) caughtType = "array";

      throw new Error(
        `[ConfirmBox] Options must be an object. Caught: ${caughtType}. Expected: object`
      );
    }

    if (
      params.hasOwnProperty("callback") &&
      typeof params.callback != "function"
    ) {
      let callbackType = typeof params.callback;

      throw new Error(
        `[ConfirmBox] Callback type must be an function. Caught: ${callbackType}. Expected: function`
      );
    }

    emitter.emit("confirmOpen", params);
  }

  /**
   * Close confirm box.
   */
  close() {
    emitter.emit("confirmClose");
  }
}

/**
 * Confirm component.
 */
export default {
  /**
   * Install plugin.
   *
   * @param {any} app
   */
  install(app) {
    if (this.installed) return;

    this.installed = true;
    const confirm = new ConfirmClass();

    app.component("v-confirm-box", ConfirmBox);
    app.provide("$confirm", confirm);
  },
};
