<template>
  <Dialog
    as="div"
    class="fixed inset-0 overflow-hidden"
    :open="isShow"
    @close="close"
  >
    <div class="absolute inset-0 overflow-hidden">
      <DialogOverlay
        class="absolute inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      />

      <div class="fixed inset-y-0 right-0 pl-10 max-w-full flex">
        <div class="relative w-96">
          <div
            class="absolute top-0 left-0 -ml-8 pt-4 pr-2 flex sm:-ml-10 sm:pr-4"
          >
            <button
              type="button"
              class="rounded-md text-gray-300 hover:text-white focus:outline-none"
              @click="close"
            >
              <span class="sr-only">Close panel</span>
              <x-mark-icon class="h-6 w-6" aria-hidden="true" />
            </button>
          </div>

          <div
            class="h-full flex flex-col justify-between bg-white overflow-y-auto"
          >
            <slot class="flex-grow" />

            <div
              v-if="$slots.actions"
              class="bg-gray-50 flex flex-row border-t border-gray-100 px-4 py-2.5 flex-none space-x-4"
            >
              <slot name="actions" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </Dialog>
</template>

<script>
import { ref } from "vue";
import { Dialog, DialogOverlay } from "@headlessui/vue";
import { HeartIcon, XMarkIcon } from "@heroicons/vue/24/outline";
import { PencilIcon } from "@heroicons/vue/20/solid";

export default {
  components: {
    Dialog,
    DialogOverlay,
    HeartIcon,
    PencilIcon,
    XMarkIcon,
  },

  setup(_, { emit }) {
    /**
     * Is show?
     *
     * @type {ToRef<boolean>}
     */
    let isShow = ref(false);

    /**
     * Open modal.
     */
    const open = () => {
      isShow.value = true;
      emit("opened");
    };

    /**
     * Close modal.
     */
    const close = () => {
      isShow.value = false;
      emit("closed");
    };

    return {
      isShow,
      open,
      close,
    };
  },
};
</script>
