<template>
  <v-modal ref="colModal" @closed="changeVisibleColumns()">
    <div class="mb-2 pb-1 pl-1 pr-1 font-semibold border-b">
      Visible columns
    </div>
    <div class="mb-2 p-1 text-sm text-gray-400">
      Click on a column name to hide or show it. Closing the window will save
      the changes!
    </div>

    <div class="grid grid-cols-2 gap-4">
      <div
        v-for="(col, index) in columns"
        :key="index"
        class="flex items-center justify-space rounded-md py-3 px-3 text-sm font-semibold sm:flex-1 border-2 cursor-pointer"
        :class="
          isVisibleColumn(col.key)
            ? 'text-green-500 border-green-400 bg-green-50 hover:border-green-200'
            : 'text-primary border-gray-100 bg-grey-50 hover:border-gray-200'
        "
        @click="toggleVisibleColumn(col.key)"
      >
        <div class="w-4 h-4 mr-2">
          <check-icon v-show="isVisibleColumn(col.key)" class="w-4 h-4" />
        </div>
        <div class="select-none">{{ col.label }}</div>
      </div>
    </div>

    <template v-slot:actions>
      <div class="flex gap-2">
        <v-button @click="selectAll" class="flex-1">
          <check-circle-icon class="w-4 h-4 mr-1.5" /> Select all
        </v-button>
        <v-button @click="unselectAll" class="flex-1">
          <x-circle-icon class="w-4 h-4 mr-1.5" /> Unselect all
        </v-button>
      </div>
    </template>
  </v-modal>
</template>

<script>
import {
  CheckIcon,
  CheckCircleIcon,
  XCircleIcon,
} from "@heroicons/vue/20/solid";
import { ref } from "vue";
import cloneDeep from "lodash.clonedeep";
import VButton from "../../button/Button.vue";
import VModal from "../../modal/Modal.vue";

export default {
  components: {
    CheckIcon,
    CheckCircleIcon,
    XCircleIcon,
    VModal,
    VButton,
  },

  props: {
    /**
     * All columns.
     */
    columns: {
      type: Array,
    },

    /**
     * Visible columns.
     */
    visibleColumns: {
      type: Array,
    },
  },

  setup(props, { emit }) {
    /**
     * Modal reference.
     */
    let colModal = ref(null);

    /**
     * Mutated visible columns array.
     */
    let visCols = ref([]);

    /**
     * Open a modal.
     */
    const open = () => {
      visCols.value = cloneDeep(props.visibleColumns);
      colModal.value.open();
    };

    /**
     * Is the column visible?
     */
    const isVisibleColumn = (colKey) => {
      return visCols.value.includes(colKey);
    };

    /**
     * Select all columns.
     */
    const selectAll = () => {
      visCols.value = cloneDeep(props.columns.map((col) => col.key));
    };

    /**
     * Unselect columns.
     */
    const unselectAll = () => {
      visCols.value = cloneDeep([]);
    };

    /**
     * Toggle visible column.
     */
    const toggleVisibleColumn = (colKey) => {
      if (visCols.value.includes(colKey)) {
        let index = visCols.value.indexOf(colKey);

        visCols.value.splice(index, 1);
      } else {
        visCols.value.push(colKey);
      }
    };

    /**
     * Change visible of columns.
     */
    const changeVisibleColumns = () => {
      emit("changed", cloneDeep(visCols.value));
      unselectAll();
    };

    return {
      colModal,
      open,
      isVisibleColumn,
      toggleVisibleColumn,
      changeVisibleColumns,
      selectAll,
      unselectAll,
    };
  },
};
</script>
