<template>
  <div class="table-datetimepicker">
    <div
      v-if="picker"
      class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
      style="z-index: 800"
      aria-hidden="true"
      @click="closePicker()"
    />

    <div class="inline-flex items-center">
      <input
        type="text"
        placeholder="Select date"
        class="bg-transparent focus:outline-none px-4 py-3 text-sm w-full"
        :class="{ 'border-red-400': error }"
        :modelValue="modelValue"
        :disabled="disabled"
        @click="showPicker"
        @update:modelValue="modelValue = $event"
        readonly
      />

      <div
        class="px-1 cursor-pointer text-gray-400 hover:text-gray-600"
        @click="clearDate()"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-4 w-4"
          viewBox="0 0 20 20"
          fill="currentColor"
        >
          <path
            fill-rule="evenodd"
            d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"
            clip-rule="evenodd"
          />
        </svg>
      </div>
    </div>

    <div v-if="picker" class="views">
      <v-years
        v-if="mode === 'years'"
        :modelValue="selectedDate"
        class="year-view"
        @mode="setMode"
      />

      <v-months
        v-else
        :modelValue="selectedDate"
        :date-format="dateFormat"
        :min-date="minDate"
        :max-date="maxDate"
        class="month-view"
        @mode="setMode"
        @selected="selectDate"
      />
    </div>
  </div>
</template>

<script>
import { ref, watch } from "vue";
import moment from "moment";

import VMonths from "./Months.vue";
import VYears from "./Years.vue";

/**
 * Initial view of picker.
 *
 * @type {String}
 */
const initialView = "month";

export default {
  components: {
    VMonths,
    VYears,
  },

  props: {
    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    modelValue: {
      type: String,
    },

    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Required.
     *
     * @type {Boolean|Object}
     */
    required: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Date format.
     */
    dateFormat: {
      type: String,
      default: "DD-MM-YYYY",
    },

    /**
     * Minimum date.
     *
     * @type {String|Object}
     */
    minDate: {
      type: String,
      default: null,
    },

    /**
     * Max date.
     *
     * @type {String|Object}
     */
    maxDate: {
      type: String,
      default: null,
    },

    /**
     * Disabled button.
     *
     * @type {Boolean|Object}
     */
    disabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Error message.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: null,
    },
  },

  setup(props, { emit }) {
    /**
     * Mode: - month, months, years, time.
     *
     * @type {Ref<UnwrapRef<String>>}
     */
    const mode = ref(initialView);

    /**
     * Picker status.
     *
     * @type {Ref<UnwrapRef<boolean>>}
     */
    const picker = ref(false);

    /**
     * Selected date.
     *
     * @type {Ref<UnwrapRef<String>>}
     */
    const selectedDate = ref(null);

    /**
     * Toggle picker.
     */
    const togglePicker = () => (picker.value = !picker.value);

    /**
     * Show picker.
     */
    const showPicker = () => (picker.value = true);

    /**
     * Close picker.
     */
    const closePicker = () => {
      if (!picker.value) {
        return;
      }

      setMode(initialView);
      picker.value = false;
    };

    /**
     * Set picker mode.
     */
    const setMode = (newMode) => (mode.value = newMode);

    /**
     * Select date.
     */
    const selectDate = () => {
      closePicker();
      emit("selected");
    };

    /**
     * Clear date.
     */
    const clearDate = () => {
      selectedDate.value = null;
      selectDate();
    };

    /**
     * Watch.
     */
    watch(picker, (isOpen) => {
      if (!isOpen || !props.modelValue) {
        return;
      }

      selectedDate.value = moment(props.modelValue, props.dateFormat);
    });

    /**
     * Watch.
     */
    watch(selectedDate, (newDate) => {
      emit(
        "update:modelValue",
        newDate ? newDate.format(props.dateFormat) : null
      );
    });

    return {
      mode,
      picker,
      selectedDate,
      togglePicker,
      showPicker,
      closePicker,
      clearDate,
      setMode,
      selectDate,
    };
  },
};
</script>
