<template>
  <div>
    <div class="flex justify-between items-center p-3">
      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="prevMonth()"
      >
        <chevron-left-icon class="h-5 w-5 text-gray-500" />
      </button>

      <div class="flex">
        <div
          class="cursor-pointer text-md font-bold text-gray-800 hover:text-gray-600 hover:underline"
          @click="goToMonths"
        >
          {{ MONTH_NAMES[getMonth] }}
        </div>
        <div
          class="cursor-pointer ml-2 text-md text-gray-600 hover:text-gray-400 hover:underline font-normal"
          @click="goToYears"
        >
          {{ getYear }}
        </div>
      </div>

      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="nextMonth()"
      >
        <chevron-right-icon class="h-5 w-5 text-gray-500" />
      </button>
    </div>

    <div class="p-3 space-y-2">
      <div class="grid grid-cols-7">
        <div v-for="(day, index) in DAYS" :key="index">
          <div class="text-gray-700 font-medium text-center text-xs">
            {{ day }}
          </div>
        </div>
      </div>

      <div class="grid grid-cols-7 -mx-1 gap-y-1">
        <div v-for="(_, index) in blankDays" :key="index">
          <div class="text-center border p-1 border-transparent text-sm"></div>
        </div>

        <div v-for="(day, dayIndex) in allDays" :key="dayIndex">
          <div class="flex items-center justify-center">
            <div
              :class="[
                getDayClasses(day),
                'flex items-center justify-center w-8 h-8 cursor-pointer text-sm rounded-full duration-200',
              ]"
              @click="selectDate(day)"
            >
              {{ day }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/vue/24/outline";
import moment from "moment";

export default {
  components: {
    ChevronLeftIcon,
    ChevronRightIcon,
  },

  props: {
    /**
     * Selected date.
     *
     * @type {moment}
     */
    selectedDate: {
      type: moment,
    },
  },

  setup(props, { emit }) {
    /**
     * Month names.
     *
     * @type {Array}
     */
    const MONTH_NAMES = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    /**
     * Days of week.
     *
     * @type {Array}
     */
    const DAYS = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];

    /**
     * Get year.
     *
     * @type {Ref<UnwrapRef<Number>>}
     */
    const getYear = ref(moment().get("year"));

    /**
     * Get month.
     *
     * @type {Ref<UnwrapRef<Number>>}
     */
    const getMonth = ref(moment().get("month"));

    /**
     * Blank days.
     *
     * @type {Ref<UnwrapRef<Array>>}
     */
    const blankDays = ref([]);

    /**
     * All month days.
     *
     * @type {Ref<UnwrapRef<Array>>}
     */
    const allDays = ref([]);

    /**
     * Go to months.
     */
    const goToMonths = () => {
      emit("setMode", "months");
    };

    /**
     * Go to years.
     */
    const goToYears = () => {
      emit("setMode", "years");
    };

    /**
     * Get month days.
     */
    const getMonthDays = () => {
      const dayNumber = new Date(getYear.value, getMonth.value).getDay();
      const dayOfWeek = dayNumber === 0 ? 6 : dayNumber - 1;
      const daysInMonth = props.selectedDate.daysInMonth();

      let blankdaysArray = [];
      for (let i = 1; i <= dayOfWeek; i++) {
        blankdaysArray.push(i);
      }

      let daysArray = [];
      for (let i = 1; i <= daysInMonth; i++) {
        daysArray.push(i);
      }

      blankDays.value = blankdaysArray;
      allDays.value = daysArray;
    };

    /**
     * Is selected?
     */
    const isSelected = (date) => {
      return props.selectedDate.format("YYYY-M-D") === date.format("YYYY-M-D");
    };

    /**
     * Get day classes.
     *
     * @param {Number} day
     *
     * @return {String}
     */
    const getDayClasses = (day) => {
      const currentDate = moment(
        `${getYear.value}-${getMonth.value + 1}-${day}`,
        "YYYY-M-D"
      );

      if (isSelected(currentDate)) {
        return ["bg-gray-600", "text-white"].join(" ");
      }

      return [
        moment().format("YYYY-M-D") === currentDate.format("YYYY-M-D")
          ? "bg-gray-200"
          : "",
        "text-gray-700",
        "hover:bg-primary",
        "hover:text-white",
      ].join(" ");
    };

    /**
     * Emit changes.
     *
     * @param {moment} date
     */
    const emitChanges = (date) => {
      emit("select", date);

      getYear.value = props.selectedDate.get("year");
      getMonth.value = props.selectedDate.get("month");
      getMonthDays();
    };

    /**
     * Set day.
     *
     * @param {Number} day
     */
    const selectDate = (day) => {
      const currentDate = moment(
        `${getYear.value}-${getMonth.value + 1}-${day}`,
        "YYYY-M-D"
      );

      emitChanges(currentDate);
    };

    /**
     * Go to the prev month.
     */
    const prevMonth = () => {
      const currentDate = props.selectedDate.subtract(1, "months");

      emitChanges(currentDate);
    };

    /**
     * Go to the next month.
     */
    const nextMonth = () => {
      const currentDate = props.selectedDate.add(1, "months");

      emitChanges(currentDate);
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      getYear.value = props.selectedDate.get("year");
      getMonth.value = props.selectedDate.get("month");

      getMonthDays();
    });

    return {
      getYear,
      getMonth,
      DAYS,
      MONTH_NAMES,
      blankDays,
      allDays,
      goToMonths,
      goToYears,
      getDayClasses,
      selectDate,
      prevMonth,
      nextMonth,
    };
  },
};
</script>
