<template>
  <div class="grid grid-cols-1" :class="[getColsNumber, getGap]">
    <slot />
  </div>
</template>

<script>
import { computed } from "vue";

export default {
  props: {
    /**
     * Number of cols.
     *
     * @type {Number|Object}
     */
    cols: {
      type: Number,
      default: 1,
    },

    /**
     * Gap.
     *
     * @type {Number|Object}
     */
    gap: {
      type: Number,
      default: 4,
    },
  },

  setup(props) {
    /**
     * Get cols number.
     */
    const getColsNumber = computed(() => {
      return "sm:grid-cols-" + props.cols;
    });

    /**
     * Get cols number.
     */
    const getGap = computed(() => {
      return "gap-" + props.gap;
    });

    return {
      getColsNumber,
      getGap,
    };
  },
};
</script>

