<template>
  <div>
    <div class="flex justify-between items-center p-3">
      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="prevYears()"
      >
        <chevron-left-icon class="h-5 w-5 text-gray-500" />
      </button>

      <span class="text-md font-bold text-gray-800">Years</span>

      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="nextYears()"
      >
        <chevron-right-icon class="h-5 w-5 text-gray-500" />
      </button>
    </div>

    <div class="p-3">
      <div class="grid grid-cols-3 gap-1">
        <div v-for="(year, yearIndex) in allYears" :key="yearIndex">
          <div class="px-2 mb-1 h-12">
            <div
              :class="[
                getYearClasses(year),
                'cursor-pointer text-center text-sm leading-none rounded leading-loose duration-100',
              ]"
              @click="selectYear(year)"
            >
              {{ year }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/vue/24/outline";
import moment from "moment";

export default {
  components: {
    ChevronLeftIcon,
    ChevronRightIcon,
  },

  props: {
    /**
     * Selected date.
     *
     * @type {moment}
     */
    selectedDate: {
      type: moment,
    },
  },

  setup(props, { emit }) {
    /**
     * All years.
     */
    const allYears = ref([]);

    /**
     * Generate years.
     */
    const generateYears = (from, to) => {
      let range = [];

      for (let i = from; i <= to; i++) {
        range.push(i);
      }

      allYears.value = range;
    };

    /**
     * Is selected?
     *
     * @param {Number} year
     *
     * @return {Boolean}
     */
    const isSelected = (year) => {
      return props.selectedDate.get("year") === year;
    };

    /**
     * Get year classes.
     *
     * @param {Number} year
     */
    const getYearClasses = (year) => {
      if (isSelected(year)) {
        return "bg-gray-600 text-white";
      }

      return "text-gray-700 hover:bg-blue-200";
    };

    /**
     * Prev years.
     */
    const prevYears = () => {
      let to = allYears.value[0] - 1;
      let from = to - 8;

      generateYears(from, to);
    };

    /**
     * Next years.
     */
    const nextYears = () => {
      let from = allYears.value[8] + 1;
      let to = from + 8;

      generateYears(from, to);
    };

    /**
     * Set year.
     *
     * @param {Number} year
     */
    const selectYear = (year) => {
      const rest = props.selectedDate.format("M-D");
      const currentDate = moment(`${year}-${rest}`, "YYYY-M-D");

      emit("select", currentDate);
      emit("setMode", "month");
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      let baseYear = props.selectedDate.get("year");

      generateYears(baseYear - 4, baseYear + 4);
    });

    return {
      allYears,
      getYearClasses,
      prevYears,
      nextYears,
      selectYear,
    };
  },
};
</script>
