<template>
  <div class="flex flex-col">
    <div class="align-middle inline-block min-w-full">
      <div class="shadow border-b border-gray-300 sm:rounded-lg">
        <div
          class="inline-flex w-full items-center justify-between bg-gray-300 p-2 border-b border-gray-400"
        >
          <div>
            <v-button @click="openColumnsModal()">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-4 w-4 mr-2"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                />
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                />
              </svg>
              Visible columns
            </v-button>
          </div>

          <div class="space-x-2">
            <v-dropdown
              v-if="canExportData"
              label="Export as"
              size="small"
              :items="exportOptions"
            />
            <v-dropdown
              :label="'Per page: ' + limit"
              size="small"
              :items="limitOptions"
            />
          </div>
        </div>

        <div class="overflow-x-auto">
          <table :id="name + 'Table'" class="min-w-full">
            <thead class="bg-gray-300">
              <tr class="divide-x divide-gray-400">
                <v-table-item
                  v-for="(col, index) in getColumns"
                  :data="col"
                  :key="index"
                />

                <th
                  v-if="actionsHas"
                  scope="col"
                  :rowspan="anySearching ? 2 : 1"
                  class="relative px-4 py-3"
                >
                  <span class="sr-only">Actions</span>
                </th>
              </tr>

              <tr
                v-if="anySearching"
                class="divide-x divide-gray-400 border-t border-b border-gray-400"
              >
                <th
                  v-for="(col, index) in getColumns"
                  :key="index"
                  scope="col"
                  class="tracking-wider"
                >
                  <div v-if="col.searchable">
                    <div
                      v-if="col.type === 'date'"
                      class="inline-flex w-full items-center justify-between"
                    >
                      <v-table-date-picker
                        name="dateFrom"
                        class="w-5/12"
                        v-model="col.searchDateFrom"
                        @selected="debounce(20)"
                      />

                      <span class="text-xl">-</span>

                      <v-table-date-picker
                        name="dateTo"
                        class="w-5/12"
                        v-model="col.searchDateTo"
                        @selected="debounce(20)"
                      />
                    </div>

                    <input
                      v-else
                      v-model="col.search"
                      class="bg-transparent focus:outline-none px-4 py-3 text-sm w-full"
                      placeholder="Type to search"
                      @input="debounce()"
                    />
                  </div>
                </th>
              </tr>
            </thead>

            <tbody v-if="isLoading">
              <tr v-for="n in limit" :key="n">
                <td
                  v-for="(col, index) in getAllColumns"
                  :key="index"
                  class="px-4 py-3 border-r border-gray-200 animate-pulse"
                >
                  <div
                    :class="`w-${Math.floor(
                      Math.random() * 12
                    )}/12 h-4 bg-gray-300 rounded`"
                  />
                </td>
              </tr>
            </tbody>

            <tbody v-else>
              <tr v-if="!visibleColumns.length">
                <td
                  :colspan="getColumns.length + 1"
                  class="text-center py-20 border-r border-gray-200 whitespace-nowrap text-sm font-medium text-gray-900"
                >
                  You must select at least one column to see the contents of the
                  table :)
                </td>
              </tr>

              <tr
                v-else-if="!data.length && !prepareSearchableColumns().length"
              >
                <td
                  :colspan="getColumns.length + 1"
                  class="text-center px-2 py-4 border-r border-gray-200 whitespace-nowrap text-sm font-medium text-gray-900"
                >
                  {{ emptyDataText }}
                </td>
              </tr>

              <tr v-else-if="!data.length && prepareSearchableColumns().length">
                <td
                  :colspan="getColumns.length + 1"
                  class="p-5 border-r border-gray-200 whitespace-nowrap text-sm font-medium text-gray-900"
                >
                  <span class="mb-4 italic">No search results for:</span>

                  <ul class="list-disc">
                    <li
                      v-for="(search, idx) in prepareSearchableColumns()"
                      class="ml-4"
                      :key="idx"
                    >
                      <span class="font-medium mr-1"
                        >{{ getColumnLabel(search) }}:</span
                      >
                      <span v-if="search.value" class="font-semibold">{{
                        search.value
                      }}</span>
                      <span v-else class="font-semibold">
                        {{ search.from || "no specific time" }} -
                        {{ search.to || "no specific time" }}
                      </span>
                    </li>
                  </ul>
                </td>
              </tr>

              <tr
                v-else
                v-for="(row, index) in getRows"
                :key="index"
                class="bg-white hover:bg-gray-100"
              >
                <td
                  v-for="(cell, index) in row.formatted"
                  :key="index"
                  class="px-4 py-3 border-r border-gray-200 whitespace-nowrap text-sm font-medium text-gray-900"
                >
                  <slot :name="row.slotName[index]" :data="row.data">
                    {{ cell }}
                  </slot>
                </td>

                <td
                  v-if="actionsHas"
                  class="px-3 py-4 whitespace-nowrap text-right text-sm font-medium"
                  :style="'width: ' + actionsWidth"
                >
                  <slot name="actions" :data="row.data" />
                </td>
              </tr>
            </tbody>
          </table>
        </div>

        <v-table-pagination
          :current-page="currentPage"
          :last-page="lastPage"
          :pagination="pagination"
          :total-items="totalItems"
          @prev="prevPage()"
          @next="nextPage()"
        />
      </div>
    </div>

    <v-table-modal
      ref="columnsModal"
      :columns="getAllColumns"
      :visible-columns="visibleColumns"
      @changed="changeVisibleColumns"
    />
  </div>
</template>

<script>
import { computed, onMounted, provide, reactive, ref, watch } from "vue";
import isUndefined from "lodash.isundefined";
import upperFirst from "lodash.upperfirst";
import kebabCase from "lodash.kebabcase";
import getValue from "lodash.get";
import some from "lodash.some";

import * as Logger from "../../helpers/Logger";

import VTableDatePicker from "../form/TableDatePicker/TableDatePicker.vue";
import VTablePagination from "./_partials/TablePagination.vue";
import VTableItem from "./_partials/TableItem.vue";
import VTableModal from "./_partials/TableModal.vue";

import VButton from "../button/Button.vue";
import VCheckbox from "../checkbox/Checkbox.vue";
import VDropdown from "../dropdown/Dropdown.vue";

const LOADING_EMIT = "loading";

export default {
  components: {
    VTableDatePicker,
    VTableItem,
    VTablePagination,
    VTableModal,
    VButton,
    VCheckbox,
    VDropdown,
  },

  props: {
    /**
     * Name of table.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Get data function.
     *
     * @type {Function|Object}
     */
    getData: {
      type: Function,
    },

    /**
     * Export data function
     */
    exportData: {
      type: Function,
      default: (data) => 1,
    },

    /**
     * Columns.
     *
     * @type {Array|Object}
     */
    columns: {
      type: Array,
    },

    /**
     * Empty data text.
     *
     * @type {String|Object}
     */
    emptyDataText: {
      type: String,
      default: "No data",
    },

    /**
     * Actions width.
     *
     * @type {String|Object}
     */
    actionsWidth: {
      type: String,
      default: "150px",
    },

    /**
     * Has actions?
     */
    actionsHas: {
      type: Boolean,
      default: () => {
        return true;
      },
    },

    /**
     * Can user export data from table?
     *
     * @type {Boolean|Object}
     */
    canExportData: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Default sort.
     *
     * @type {Object}
     */
    defaultSort: {
      type: Object,
      default: () => {
        return {
          prop: "created_at",
          order: "desc",
        };
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Data.
     */
    let data = ref([]);

    /**
     * Columns modal.
     */
    let columnsModal = ref(null);

    /**
     * Is loading?
     */
    let isLoading = ref(false);
    provide("isLoading", isLoading);

    /**
     * Current page.
     */
    let currentPage = ref(1);

    /**
     * Total items.
     */
    let totalItems = ref(0);

    /**
     * Last page.
     */
    let lastPage = ref(1);

    /**
     * Limit of page.
     */
    let limit = ref(5);

    /**
     * Limit options.
     *
     * @type {Array}
     */
    let limitOptions = [
      { label: "5", click: () => changeLimit(5) },
      { label: "10", click: () => changeLimit(10) },
      { label: "25", click: () => changeLimit(25) },
      { label: "50", click: () => changeLimit(50) },
      { label: "100", click: () => changeLimit(100) },
    ];

    /**
     * Available export options.
     *
     * @type {Array}
     */
    let exportOptions = [
      { label: "Copy", click: () => exportDataProcess("copy") },
      { label: "Excel", click: () => exportDataProcess("excel") },
      { label: "CSV", click: () => exportDataProcess("csv") },
      { label: "PDF", click: () => exportDataProcess("pdf") },
    ];

    /**
     * Pagination.
     */
    let pagination = ref({ from: 0, to: 0 });

    /**
     * Visible columns.
     *
     * @type {ToRef<array>}
     */
    let visibleColumns = ref([]);

    /**
     * Sort by.
     */
    let sortBy = reactive(props.defaultSort);
    provide("sortBy", sortBy);

    /**
     * Settings name.
     */
    let settingsName = ref(kebabCase(`${props.name}-settings`));

    /**
     * Has table settings.
     *
     * @return {Boolean}
     */
    let hasTableSettings = computed(
      () => !!localStorage.getItem(settingsName.value)
    );

    /**
     * Get table settings.
     *
     * @return {Object}
     */
    let getTableSettings = computed(() =>
      hasTableSettings.value
        ? JSON.parse(localStorage.getItem(settingsName.value))
        : {}
    );

    /**
     * Set table settings.
     *
     * @param {String} key
     * @param {String|Object} value
     */
    const setTableSettings = (key, value) => {
      let result = getTableSettings.value;

      result[key] = value;

      localStorage.setItem(settingsName.value, JSON.stringify(result));
    };

    /**
     * Get columns.
     */
    const getColumns = computed(() => {
      return props.columns
        .filter((col) => isVisibleColumn(col.key))
        .map((col) => {
          return {
            key: col.key,
            label: getColumnLabel(col),
            search: null,
            searchDateFrom: null,
            searchDateTo: null,
            searchable: isUndefined(col.searchable) ? false : col.searchable,
            sortable: isUndefined(col.sortable) ? false : col.sortable,
            type: isUndefined(col.type) ? "string" : col.type,
            width: isUndefined(col.width) ? "250px" : col.width,
          };
        });
    });

    /**
     * Get all columns.
     */
    const getAllColumns = computed(() => {
      return props.columns.map((col) => {
        return {
          key: col.key,
          label: getColumnLabel(col),
        };
      });
    });

    /**
     * Is any searching?
     */
    const anySearching = computed(() => {
      return some(getColumns.value, "searchable");
    });

    /**
     * Is visible column?
     *
     * @param {String} colKey
     *
     * @return {Boolean};
     */
    const isVisibleColumn = (colKey) => {
      return visibleColumns.value.includes(colKey);
    };

    /**
     * Get rows.
     *
     * @type {ComputedRef<Array>}
     */
    const getRows = computed(() => {
      return data.value.map((row) => {
        return {
          formatted: getColumns.value.map((col) =>
            getValue(row, col.key, null)
          ),
          type: getColumns.value.map((col) => col.type),
          slotName: getColumns.value.map((col) => getSlotName(col)),
          data: row,
        };
      });
    });

    /**
     * Get slot name.
     *
     * @param {Object} col
     *
     * @return {String}
     */
    const getSlotName = (col) => {
      if (col.slotName) {
        return col.slotName;
      }

      return col.key.replace(/\.|_/g, "-");
    };

    /**
     * Get visible columns.
     */
    const getVisibleColumns = () => {
      if (
        !hasTableSettings.value ||
        !getTableSettings.value.hasOwnProperty("visibleColumns")
      ) {
        Logger.log("- Table: set all columns");

        return getAllColumns.value.map((col) => col.key);
      }
      Logger.log("- Table: set columns from cookie");

      return getTableSettings.value.visibleColumns;
    };

    /**
     * Set default.
     */
    const setDefault = () => {
      visibleColumns.value = getVisibleColumns();

      if (!hasTableSettings.value) {
        return;
      }

      let settings = getTableSettings.value;

      if (settings.hasOwnProperty("sortBy")) {
        sortBy.prop = settings.sortBy.prop;
        sortBy.order = settings.sortBy.order;
      }

      if (settings.hasOwnProperty("limit")) {
        limit.value = settings.limit;
      }
    };

    /**
     * Get column label.
     *
     * @return {String}
     */
    const getColumnLabel = (col) => {
      if (col.hasOwnProperty("label")) {
        return col.label;
      }

      let label = col.key.replaceAll("_", " ").replaceAll(".", " ");

      return upperFirst(label);
    };

    /**
     * Prepare searchable columns.
     */
    const prepareSearchableColumns = () => {
      return getColumns.value
        .filter((col) => col.search || col.searchDateFrom || col.searchDateTo)
        .map((col) => {
          if (col.type === "date") {
            return {
              label: col.label,
              key: col.key,
              from: col.searchDateFrom,
              to: col.searchDateTo,
            };
          }

          return { label: col.label, key: col.key, value: col.search };
        });
    };

    /**
     * Refresh table.
     */
    const refreshTable = async () => {
      isLoading.value = true;
      emit(LOADING_EMIT, true);

      let params = {
        page: currentPage.value,
        limit: limit.value,
        sort_by: sortBy,
        search: prepareSearchableColumns(),
      };

      await props
        .getData(params)
        .then(({ data: response }) => {
          data.value = response.data;
          currentPage.value = response.current_page;
          totalItems.value = response.total;
          lastPage.value = response.last_page;
          pagination.value = {
            from: response.from,
            to: response.to,
          };
        })
        .finally(() => {
          isLoading.value = false;
          emit(LOADING_EMIT, false);
        });
    };

    /**
     * Reset current page.
     */
    const resetCurrentPage = () => (currentPage.value = 1);

    /**
     * Search value.
     */
    const searchValue = () => {
      let timeout = null;

      return function (delayMs) {
        clearTimeout(timeout);

        timeout = setTimeout(async () => {
          resetCurrentPage();
          await refreshTable();
        }, delayMs || 500);
      };
    };

    /**
     * Current page was changed!
     *
     * @returns {Promise<void>}
     */
    const changedCurrentPage = async () => {
      emit("changePage", currentPage.value);

      await refreshTable();
    };

    /**
     * Go to the previous page.
     */
    const prevPage = async () => {
      currentPage.value = currentPage.value - 1;
      await changedCurrentPage();
    };

    /**
     * Go to the next page.
     */
    const nextPage = async () => {
      currentPage.value = currentPage.value + 1;
      await changedCurrentPage();
    };

    /**
     * Change limit.
     */
    const changeLimit = async (newLimit) => {
      setTableSettings("limit", newLimit);
      limit.value = newLimit;

      await refreshTable();
    };

    /**
     * Change visible columns.
     */
    const changeVisibleColumns = (columns) => {
      visibleColumns.value = columns;
      setTableSettings("visibleColumns", columns);
    };

    /**
     * Open columns modal.
     */
    const openColumnsModal = () => {
      columnsModal.value.open();
    };

    /**
     * Export data process.
     */
    const exportDataProcess = async (type) => {
      isLoading.value = true;

      let params = {
        sort_by: sortBy,
        search: prepareSearchableColumns(),
        columns: getColumns.value.map((col) => ({
          key: col.key,
          label: col.label,
        })),
        type,
      };

      await props
        .exportData(params)
        .then(({ data: response }) => {
          Logger.log(JSON.stringify(response));

          if (type === "copy") {
            navigator.clipboard.writeText(response.text);
            return;
          }

          window.open(response.uri);
        })
        .finally(() => (isLoading.value = false));
    };

    /**
     * On mounted.
     */
    onMounted(async () => {
      setDefault();
      await refreshTable();
    });

    /**
     * Sorting watcher.
     */
    watch(sortBy, async () => {
      setTableSettings("sortBy", { order: sortBy.order, prop: sortBy.prop });

      resetCurrentPage();
      await refreshTable();
    });

    return {
      data,
      pagination,
      totalItems,
      currentPage,
      lastPage,
      getColumns,
      getAllColumns,
      getRows,
      sortBy,
      limit,
      limitOptions,
      exportOptions,
      columnsModal,
      anySearching,
      isVisibleColumn,
      getColumnLabel,
      prevPage,
      nextPage,
      prepareSearchableColumns,
      openColumnsModal,
      visibleColumns,
      changeVisibleColumns,
      refreshTable,
      debounce: searchValue(),
      isLoading,
    };
  },
};
</script>
