/**
 * Is Server.
 *
 * @param vNode
 * @return {boolean}
 */
function isServer(vNode) {
  return (
    typeof vNode.componentInstance !== "undefined" &&
    vNode.componentInstance.$isServer
  );
}

/**
 * Before mount.
 *
 * @param el
 * @param binding
 * @param vNode
 */
function beforeMount(el, binding, vNode) {
  function handler(e) {
    let elements = e.path || (e.composedPath && e.composedPath());
    elements && elements.length > 0 && elements.unshift(e.target);

    if (el.contains(e.target)) return;

    el.__vueClickOutside__.callback(e);
  }

  el.__vueClickOutside__ = {
    handler: handler,
    callback: binding.value,
  };

  const clickHandler =
    "ontouchstart" in document.documentElement ? "touchstart" : "click";
  !isServer(vNode) && document.addEventListener(clickHandler, handler);
}

/**
 * Unmounted.
 *
 * @param el
 * @param binding
 * @param vNode
 */
function unmounted(el, binding, vNode) {
  const clickHandler =
    "ontouchstart" in document.documentElement ? "touchstart" : "click";

  !isServer(vNode) &&
    el.__vueClickOutside__ &&
    document.removeEventListener(clickHandler, el.__vueClickOutside__.handler);

  delete el.__vueClickOutside__;
}

/**
 * Export.
 */
export default {
  /**
   * Install plugin.
   *
   * @param {any} app
   */
  install(app) {
    app.directive("click-outside", {
      beforeMount,
      unmounted,
    });
  },
};
