<template>
  <div class="relative">
    <v-label
      v-if="label"
      :label="label"
      :name="name"
      :is-required="isRequired"
      :is-optional="isOptional"
    />

    <div class="my-1.5 flex rounded-md shadow-sm">
      <div class="relative flex items-stretch flex-grow">
        <div
          class="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none"
        >
          <calendar-icon class="w-4 h-4 text-gray-500" />
        </div>

        <input
          type="text"
          placeholder="Select date"
          class="block border text-sm pl-9 p-2 rounded-none rounded-l-md w-full pr-10 focus:outline-none"
          :class="[getErrorClass, getDisabledClass]"
          v-model="displaySelectedDate"
          @click="openPicker"
          readonly
        />
      </div>

      <button
        class="-ml-px relative inline-flex items-center space-x-2 px-4 py-2 bg-gray-200 border text-sm font-medium rounded-r-md text-gray-700 focus:outline-none"
        :class="[
          getErrorClass,
          getDisabledClass,
          error ? 'text-red-700' : 'text-gray-700',
        ]"
        @click="clearDate()"
      >
        <x-circle-icon class="h-4 w-4" />
      </button>
    </div>

    <error-message v-if="error">{{ error }}</error-message>

    <v-modal ref="modal" @closed="closePicker">
      <month
        v-if="mode === 'month'"
        :selected-date="selectedDate"
        @select="selectDate"
        @setMode="setMode"
      />
      <months
        v-else-if="mode === 'months'"
        :selected-date="selectedDate"
        @select="selectDate"
        @setMode="setMode"
      />
      <years
        v-else-if="mode === 'years'"
        :selected-date="selectedDate"
        @select="selectDate"
        @setMode="setMode"
      />
    </v-modal>
  </div>
</template>

<script>
import { CalendarIcon } from "@heroicons/vue/24/outline";
import { XCircleIcon } from "@heroicons/vue/20/solid";
import { ref, computed, onMounted, watch } from "vue";
import VLabel from "../label/Label.vue";
import moment from "moment";
import VButton from "../button/Button.vue";
import VModal from "../modal/Modal.vue";

import Month from "./_partials/Month.vue";
import Months from "./_partials/Months.vue";
import Years from "./_partials/Years.vue";
import ErrorMessage from "../error-message/ErrorMessage.vue";

export default {
  components: {
    ErrorMessage,
    CalendarIcon,
    Month,
    Months,
    VButton,
    VLabel,
    VModal,
    XCircleIcon,
    Years,
  },

  props: {
    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    modelValue: {
      type: String,
    },

    /**
     * Error message.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: null,
    },

    /**
     * Date format.
     */
    dateFormat: {
      type: String,
      default: "YYYY-MM-DD",
    },

    /**
     * Display date format.
     */
    displayDateFormat: {
      type: String,
      default: "DD/MM/YYYY",
    },

    /**
     * Required.
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is optional?
     *
     * @type {Boolean|Object}
     */
    isOptional: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props, { emit }) {
    let modal = ref(null);

    /**
     * Selected date.
     */
    let selectedDate = ref(null);

    /**
     * Current mode.
     *
     * Available modes: month, months, years.
     */
    let mode = ref("month");

    /**
     * Is human selected the date?
     */
    let humanSelectedDate = ref(false);

    /**
     * Has error.
     *
     * @return {boolean}
     */
    const hasError = computed(() => {
      return !!props.error;
    });

    /**
     * Get error class.
     */
    const getErrorClass = computed(() => {
      return hasError.value
        ? "border-red-500 hover:border-red-600"
        : "border-gray-300 hover:border-gray-400";
    });

    /**
     * Get disabled class.
     */
    const getDisabledClass = computed(() => {
      return props.isDisabled ? "bg-gray-200 text-gray-500" : null;
    });

    /**
     * Display selected date.
     *
     * @return {String}
     */
    const displaySelectedDate = computed(() => {
      if (!selectedDate.value) return null;

      return selectedDate.value.format(props.displayDateFormat);
    });

    /**
     * Set mode.
     *
     * @param {String} newMode
     */
    const setMode = (newMode) => {
      mode.value = newMode;
    };

    /**
     * Open picker.
     */
    const openPicker = () => {
      selectedDate.value = props.modelValue
        ? moment(props.modelValue, props.dateFormat)
        : moment();

      modal.value.open();
    };

    /**
     * Close picker.
     */
    const closePicker = () => {
      if (selectedDate.value) {
        emit("update:modelValue", selectedDate.value.format(props.dateFormat));
      }

      humanSelectedDate.value = false;
    };

    /**
     * Select date.
     *
     * @param {moment} newDate
     */
    const selectDate = (newDate) => {
      humanSelectedDate.value = true;
      selectedDate.value = newDate;
    };

    /**
     * Clear date.
     */
    const clearDate = () => {
      humanSelectedDate.value = false;
      selectedDate.value = null;

      emit("update:modelValue", null);
    };

    /**
     * OnMounted.
     */
    onMounted(() => {
      selectedDate.value = props.modelValue
        ? moment(props.modelValue, props.dateFormat)
        : null;
    });

    /**
     * Watcher.
     */
    watch(
      () => props.modelValue,
      (value) => selectDate(value ? moment(value, props.dateFormat) : null)
    );

    return {
      modal,
      mode,
      selectedDate,
      getErrorClass,
      getDisabledClass,
      displaySelectedDate,
      setMode,
      openPicker,
      closePicker,
      selectDate,
      clearDate,
    };
  },
};
</script>
