<template>
  <div class="shadow border border-gray-200 hover:border-gray-300 bg-gray-100 divide-y divide-gray-200 justify-between">
    <div class="flex flex-row bg-white divide-x divide-gray-200" @click="openFile()"
         :class="{'cursor-pointer': canOpenFile}">
      <div class="p-3 items-center text-gray-300">
        <svg xmlns="http://www.w3.org/2000/svg"
             class="h-12 w-12"
             fill="none"
             viewBox="0 0 24 24"
             stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
                d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
        </svg>
      </div>

      <div class="p-3 flex-grow">
        <div class="text-primary text-sm font-semibold">{{ mFile.name }}</div>
        <div class="text-gray-500 text-xs mt-1">{{ mFile.type }}</div>
      </div>
    </div>

    <div v-if="canShowActions" class="flex flex-row justify-between items-center p-2 text-right">
      <div class="px-2">
        <span v-if="isUploading" class="text-gray-400 text-sm">Uploading...</span>
        <span v-else-if="isRemoving" class="text-green-600 text-sm">Removing...</span>
        <span v-else-if="mFile.status === 3" class="inline-flex items-center text-red-400 text-sm">
          <svg xmlns="http://www.w3.org/2000/svg"
               class="h-5 w-5 pr-1.5"
               viewBox="0 0 20 20"
               fill="currentColor">
            <path fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7 4a1 1 0 11-2 0 1 1 0 012 0zm-1-9a1 1 0 00-1 1v4a1 1 0 102 0V6a1 1 0 00-1-1z" />
          </svg>
          {{ errorMsg }}
        </span>
      </div>

      <div>
        <button v-if="mFile.status === 1"
                class="inline-flex items-center text-sm text-gray-500 hover:text-red-800"
                @click="remove()">
          <svg xmlns="http://www.w3.org/2000/svg"
               class="h-6 w-6 pr-2"
               fill="none"
               viewBox="0 0 24 24"
               stroke="currentColor">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
          </svg>

          Delete
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, inject, ref } from "vue";
import { useStore } from "vuex";

export default {
  props: {
    /**
     * File resource.
     *
     * @type {Object}
     */
    file: {
      type: Object,
    },

    /**
     * Can show actions buttons?
     *
     * @type {Boolean|Object}
     */
    canShowActions: {
      type: Boolean,
      default: () => {
        return true;
      },
    },

    /**
     * Can user open file?
     *
     * @type {Boolean|Object}
     */
    canOpenFile: {
      type: Boolean,
      default: () => {
        return true;
      },
    },
  },

  setup(props) {
    /**
     * Store.
     */
    const { commit } = useStore();

    /**
     * API.
     */
    const $api = inject("$api");

    /**
     * Notify.
     */
    const $notify = inject("$notify");

    /**
     * Mutable file resource.
     */
    let mFile = ref(props.file);

    /**
     * Error message.
     *
     * @type {ToRef<Boolean>}
     */
    let errorMsg = ref(null);

    /**
     * Is uploading?
     *
     * @type {ToRef<Boolean>}
     */
    let isUploading = ref(false);

    /**
     * Is removing?
     *
     * @type {ToRef<Boolean>}
     */
    let isRemoving = ref(false);

    /**
     * Upload file.
     *
     * @return {Promise<void>}
     */
    const upload = async () => {
      isUploading.value = true;

      const formPayload = new FormData();

      formPayload.append("file", mFile.value.data);
      formPayload.append("model_type", mFile.value.modelType);
      formPayload.append("model_id", mFile.value.modelId);

      await $api.post("/files", formPayload)
        .then(({ data: response }) => {
          mFile.value.status = 1;
          mFile.value.id = response.file_id;

          $notify({
            title: "Success",
            message: "The file has been uploaded correctly",
            type: "success",
          });
        })
        .catch((result) => {
          mFile.value.status = 3;

          $notify({
            title: "Error",
            message: result.errors.file,
            type: "danger",
          });
        })
        .finally(() => isUploading.value = false);
    };

    /**
     * Remove file.
     *
     * @return {Promise<void>}
     */
    const remove = async () => {
      isRemoving.value = true;

      await $api.delete("/files/" + mFile.value.id)
        .then(() => {
          commit("files/remove", mFile.value.id);
        })
        .finally(() => isRemoving.value = false);
    };

    /**
     * Open file.
     *
     * @return {Promise<void>}
     */
    const openFile = async () => {
      await $api.get("/files/" + mFile.value.id)
        .then(({ data: response }) => window.open(response.link));
    };

    /**
     * On mounted.
     */
    onMounted(async () => {
      if (mFile.value.status !== 0) return;

      await upload();
    });

    return {
      isUploading,
      isRemoving,
      errorMsg,
      mFile,
      remove,
      openFile,
    };
  },
};
</script>
