<template>
  <div @drop.prevent="onDrop($event)"
       @dragover.prevent="dragover"
       @dragenter.prevent="dragenter"
       @dragleave.prevent="dragleave"
       class="border-dashed border-2 py-12 flex flex-col justify-center items-center"
       :class="[isDragover ? 'border-gray-200 text-gray-600': 'border-gray-400 text-gray-900']">
    <p class="mb-3 text-sm flex flex-wrap justify-center">
      <span v-if="isDragover">Drop file(s) to add them to the upload queue</span>
      <span v-else class="text-center">Drag and drop your files anywhere<br>or</span>
    </p>

    <input ref="selectFile"
           type="file"
           class="hidden"
           @change="onSelectFiles"
           multiple />

    <v-button class="w-48 mt-2"
              @click="selectFileOpen()"
              :is-disabled="isDragover">
      Click here
    </v-button>
  </div>
</template>

<script>
import { ref } from "vue";
import { useStore } from "vuex";
import forEach from "lodash.foreach";
import { v4 as uuid } from "uuid";

export default {
  props: {
    /**
     * Model type.
     *
     * @type {String|Object}
     */
    modelType: {
      type: String,
    },

    /**
     * Model ID.
     *
     * @type {String|Object}
     */
    modelId: {
      type: String,
    },
  },

  setup(props) {
    /**
     * Store.
     */
    const { commit } = useStore();

    /**
     * Is dragover?
     */
    let isDragover = ref(false);

    /**
     * Select file.
     */
    const selectFile = ref(null);

    /**
     * File resource.
     *
     * @param {Object} data
     *
     * @return {Object}
     */
    const fileResource = (data) => {
      return {
        id: data.hasOwnProperty("id") ? data.id : uuid(),
        name: data.name,
        status: data.hasOwnProperty("status") ? data.status : 0,
        type: data.type,
        modelType: props.modelType,
        modelId: props.modelId,
        data,
      };
    };

    /**
     * Select file open.
     */
    const selectFileOpen = () => {
      selectFile.value.click();
    };

    /**
     * Add to queue.
     *
     * @param {FileList} selectedFiles
     */
    const upload = (selectedFiles) => {
      forEach(selectedFiles, (file) => {
        commit("files/push", fileResource(file));
      });
    };

    /**
     * On drop.
     *
     * @param e
     */
    const onDrop = (e) => {
      upload(e.dataTransfer.files);
      isDragover.value = false;
    };

    /**
     * On select file(s).
     *
     * @param {Object} target
     */
    const onSelectFiles = ({ target }) => {
      upload(target.files);
    };

    /**
     * Dragover.
     */
    const dragover = () => {
      isDragover.value = true;
    };

    /**
     * Dragenter.
     */
    const dragenter = () => {
      isDragover.value = true;
    };

    /**
     * Dragleave.
     */
    const dragleave = () => {
      isDragover.value = false;
    };

    return {
      isDragover,
      selectFile,
      selectFileOpen,
      onDrop,
      onSelectFiles,
      dragover,
      dragenter,
      dragleave,
    };
  },
};
</script>
