<template>
  <div class="grid grid-cols-3 gap-6">
    <v-file v-for="(file, index) in files"
            :file="file"
            :key="index"
            :can-show-actions="canShowActions"
            :can-open-file="canOpenFile" />
  </div>
</template>

<script>
import { inject, computed, onMounted } from "vue";
import { useStore } from "vuex";

import VFile from "./File.vue";

export default {
  components: {
    VFile,
  },

  props: {
    /**
     * Model type.
     *
     * @type {String|Object}
     */
    modelType: {
      type: String,
    },

    /**
     * Model ID.
     *
     * @type {String|Object}
     */
    modelId: {
      type: String,
    },

    /**
     * Can show actions buttons?
     *
     * @type {Boolean|Object}
     */
    canShowActions: {
      type: Boolean,
      default: () => {
        return true;
      },
    },

    /**
     * Can user open file?
     *
     * @type {Boolean|Object}
     */
    canOpenFile: {
      type: Boolean,
      default: () => {
        return true;
      },
    },
  },

  setup(props) {
    /**
     * Store.
     */
    const { state, commit } = useStore();

    /**
     * API.
     */
    const $api = inject("$api");

    /**
     * Refresh files resource.
     */
    const refresh = async () => {
      await $api.get("/files?model_type=" + props.modelType + "&model_id=" + props.modelId)
        .then(({ data: response }) => {
          commit("files/setFiles", response);
        });
    };

    /**
     * On mounted.
     */
    onMounted(async () => {
      await refresh();
    });

    return {
      files: computed(() => state.files.list),
      refresh,
    };
  },
};
</script>
