<template>
  <div>
    <div class="flex justify-between items-center mb-2">
      <button
        type="button"
        @click="prevYears()">
        <svg class="h-6 w-6 text-gray-500 inline-flex" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
        </svg>
      </button>

      <span class="text-md font-bold text-gray-800">Years</span>

      <button
        type="button"
        @click="nextYears()">
        <svg class="h-6 w-6 text-gray-500 inline-flex" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
        </svg>
      </button>
    </div>

    <div class="grid grid-cols-3 -mx-1">
      <div v-for="(year, yearIndex) in allYears" :key="yearIndex">
        <div class="px-2 mb-1 h-12">
          <div
            :class="[getYearClasses(year), 'cursor-pointer text-center text-sm leading-none rounded leading-loose duration-100']"
            @click="selectYear(year)">
            {{ year }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { onMounted, ref } from "vue";
import moment from "moment";

export default {
  name: "v-date-picker-years",

  props: {
    /**
     * V-model.
     *
     * @type {Boolean|Object}
     */
    modelValue: {
      type: String,
    },
  },

  setup(props, { emit }) {
    /**
     * All years.
     */
    const allYears = ref([]);

    /**
     * Selected year.
     */
    const selectedYear = ref(0);

    /**
     * Generate years.
     */
    const generateYears = (from, to) => {
      let range = [];

      for (let i = from; i <= to; i++) {
        range.push(i);
      }

      allYears.value = range;
    };

    /**
     * Prev years.
     */
    const prevYears = () => {
      let to = allYears.value[0] - 1;
      let from = to - 8;

      generateYears(from, to);
    };

    /**
     * Next years.
     */
    const nextYears = () => {
      let from = allYears.value[8] + 1;
      let to = from + 8;

      generateYears(from, to);
    };

    /**
     * Is selected?
     */
    const isSelected = (year) => {
      return year === selectedYear.value;
    };

    /**
     * Get year classes.
     *
     * @param {String} year
     */
    const getYearClasses = (year) => {
      if (isSelected(year)) {
        return "bg-gray-600 text-white";
      }

      return "text-gray-700 hover:bg-blue-200";
    };

    /**
     * Select year.
     */
    const selectYear = (year) => {
      let baseDate = props.modelValue ?? moment();
      selectedYear.value = year;

      emit("update:modelValue", baseDate.year(year));
      emit("mode", "month");
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      let baseDate = props.modelValue ?? moment();
      selectedYear.value = baseDate.get("year");

      generateYears(selectedYear.value - 4, selectedYear.value + 4);
    });

    return {
      allYears,
      prevYears,
      nextYears,
      getYearClasses,
      selectYear,
    };
  },
};
</script>
