<template>
  <div class="relative">
    <div class="flex items-center">
      <div class="flex-none w-7">
        <input
          :id="name"
          :name="name"
          v-model="checkbox"
          type="checkbox"
          :disabled="isDisabled"
          :class="[getErrorClass, getDisabledClass]"
          class="border h-4 w-4 rounded focus:outline-none"
        />
      </div>

      <div class="flex-grow">
        <v-label :name="name" :label="label" :is-required="isRequired" />
      </div>
    </div>

    <div v-if="subtitle || error" class="flex items-center">
      <div class="flex-none w-7" />

      <div>
        <div class="text-gray-400 text-xs">{{ subtitle }}</div>
        <error-message v-if="error">{{ error }}</error-message>
      </div>

    </div>
  </div>
</template>

<script>
import { computed } from "vue";

import VLabel from "../label/Label.vue";
import ErrorMessage from "../error-message/ErrorMessage.vue";

export default {
  components: {
    ErrorMessage,
    VLabel,
  },

  props: {
    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * V-model value.
     *
     * @type {String|Object}
     */
    modelValue: {
      type: Boolean,
    },

    /**
     * Subtitle.
     *
     * @type {String|Object}
     */
    subtitle: {
      type: String,
      default: null,
    },

    /**
     * Error.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: () => {
        return null;
      },
    },

    /**
     * Is Required?
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is disabled?
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Checkbox.
     */
    const checkbox = computed({
      get: () => props.modelValue,
      set: (value) => emit(`update:modelValue`, value),
    });

    /**
     * Has error.
     *
     * @return {boolean}
     */
    const hasError = computed(() => {
      return !!props.error;
    });

    /**
     * Get error class.
     */
    const getErrorClass = computed(() => {
      return hasError.value ? "border-red-500 focus:border-red-600" : "border-gray-300 focus:border-gray-400";
    });

    /**
     * Get disabled class.
     */
    const getDisabledClass = computed(() => {
      return props.isDisabled ? "border-gray-200" : null;
    });

    return {
      checkbox,
      getErrorClass,
      getDisabledClass,
    };
  },
};
</script>
