<template>
  <div>
    <div class="flex justify-between items-center p-3">
      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="prevYear()"
      >
        <chevron-left-icon class="h-5 w-5 text-gray-500" />
      </button>

      <div>
        <span class="text-md font-bold text-gray-800">{{ getYear }}</span>
      </div>

      <button
        class="transition ease-in-out duration-100 inline-flex cursor-pointer hover:bg-gray-200 p-1 rounded-full"
        type="button"
        @click="nextYear()"
      >
        <chevron-right-icon class="h-5 w-5 text-gray-500" />
      </button>
    </div>

    <div class="p-3">
      <div class="grid grid-cols-4 gap-1">
        <div v-for="(month, monthIndex) in MONTH_NAMES" :key="monthIndex">
          <div class="flex items-center justify-center">
            <div
              :class="[
                getMonthClasses(monthIndex),
                'flex w-full items-center justify-center px-5 py-3 cursor-pointer text-sm rounded duration-200',
              ]"
              @click="selectMonth(monthIndex)"
            >
              {{ month }}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import { ref, onMounted } from "vue";
import { ChevronLeftIcon, ChevronRightIcon } from "@heroicons/vue/24/outline";

export default {
  components: {
    ChevronLeftIcon,
    ChevronRightIcon,
  },

  props: {
    /**
     * Selected date.
     *
     * @type {moment}
     */
    selectedDate: {
      type: moment,
    },
  },

  setup(props, { emit }) {
    /**
     * Month names.
     *
     * @type {Array}
     */
    const MONTH_NAMES = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    /**
     * Get year.
     *
     * @type {Ref<UnwrapRef<Number>>}
     */
    const getYear = ref(moment().get("year"));

    /**
     * Is selected?
     *
     * @param {Number} month
     *
     * @return {Boolean}
     */
    const isSelected = (month) => {
      return props.selectedDate.get("month") === month;
    };

    /**
     * Get month classes.
     *
     * @param {Number} month
     *
     * @return {String}
     */
    const getMonthClasses = (month) => {
      if (isSelected(month)) {
        return "bg-gray-600 text-white";
      }

      return "text-gray-700 hover:bg-primary hover:text-white";
    };

    /**
     * Emit changes.
     *
     * @param {moment} date
     */
    const emitChanges = (date) => {
      emit("select", date);

      getYear.value = props.selectedDate.get("year");
    };

    /**
     * Set day.
     *
     * @param {Number} month
     */
    const selectMonth = (month) => {
      const day = props.selectedDate.format("D");
      const currentDate = moment(
        `${getYear.value}-${month + 1}-${day}`,
        "YYYY-M-D"
      );

      emitChanges(currentDate);
      emit("setMode", "month");
    };

    /**
     * Go to the prev year.
     */
    const prevYear = () => {
      const currentDate = props.selectedDate.subtract(1, "years");

      emitChanges(currentDate);
    };

    /**
     * Go to the next year.
     */
    const nextYear = () => {
      const currentDate = props.selectedDate.add(1, "years");

      emitChanges(currentDate);
    };

    /**
     * On mounted.
     */
    onMounted(() => {
      getYear.value = props.selectedDate.get("year");
    });

    return {
      MONTH_NAMES,
      getYear,
      selectMonth,
      getMonthClasses,
      prevYear,
      nextYear,
    };
  },
};
</script>
