<template>
  <div class="bg-white shadow-md rounded divide-y divide-gray-200">
    <div v-if="title" class="p-4 font-medium text-lg bg-primary text-white rounded-t">
      <h3 v-if="title" class="text-lg leading-6 font-medium">{{ title }}</h3>
      <p v-if="subtitle" class="mt-1 max-w-2xl text-xs text-gray-400">{{ subtitle }}</p>
    </div>

    <div :class="bodyClass">
      <slot />
    </div>

    <div v-if="hasFooter" class="p-2 text-right bg-gray-100 rounded-b">
      <slot name="footer" />
    </div>
  </div>
</template>

<script>
import { computed } from "vue";
import isUndefined from "lodash.isundefined";

export default {
  props: {
    /**
     * Title of card.
     *
     * @type {String|Object}
     */
    title: {
      type: String,
      default: null,
    },

    /**
     * Subtitle of card.
     *
     * @type {String|Object}
     */
    subtitle: {
      type: String,
      default: null,
    },

    /**
     * Body class.
     *
     * @type {String|Object}
     */
    bodyClass: {
      type: String,
      default: "p-3",
    },
  },

  setup(props, { slots }) {
    /**
     * Has footer.
     *
     * @return {Boolean}
     */
    const hasFooter = computed(() => {
      return !isUndefined(slots.footer);
    });

    return {
      hasFooter,
    };
  },
};
</script>
