<template>
  <Listbox
    as="div"
    v-model="selectedValues"
    :multiple="multiple"
    :disabled="isDisabled"
  >
    <div v-if="label" class="flex justify-between">
      <label :for="name" class="block text-sm font-medium text-gray-700">
        {{ label }}
        <span v-if="isRequired" class="text-xs text-red-700">*</span>
      </label>

      <span v-if="isOptional" class="text-xs text-gray-500">Optional</span>
    </div>

    <div class="relative my-1.5">
      <ListboxButton
        class="relative mx-auto p-2 w-full flex items-center cursor-pointer border rounded-md bg-white text-sm leading-snug outline-none shadow-sm"
        :class="[getErrorClass]"
      >
        <span class="block truncate pr-6">{{ getSelectedValues }}</span>
        <span
          class="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2"
        >
          <ChevronDownIcon class="h-5 w-5 text-gray-400" aria-hidden="true" />
        </span>
      </ListboxButton>

      <transition
        leave-active-class="transition ease-in duration-100"
        leave-from-class="opacity-100"
        leave-to-class="opacity-0"
      >
        <ListboxOptions
          class="absolute z-10 mt-1 max-h-60 w-full overflow-auto rounded-md bg-white py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm"
        >
          <ListboxOption
            as="template"
            v-for="option in getOptions"
            :key="option.value"
            :value="option"
            v-slot="{ active, selected }"
          >
            <li
              :class="[
                selected ? 'bg-gray-100' : null,
                active ? 'text-gray-600 bg-gray-200' : 'text-primary',
                'relative cursor-default select-none py-2 pl-3 pr-9',
              ]"
            >
              <span
                :class="[
                  selected ? 'font-semibold' : 'font-normal',
                  'block truncate',
                ]"
                >{{ option.label }}</span
              >

              <span
                v-if="selected"
                :class="[
                  active ? 'text-gray-600' : 'text-primary',
                  'absolute inset-y-0 right-0 flex items-center pr-4',
                ]"
              >
                <CheckIcon class="h-5 w-5" aria-hidden="true" />
              </span>
            </li>
          </ListboxOption>
        </ListboxOptions>
      </transition>
    </div>

    <error-message v-if="hasError">{{ error }}</error-message>
  </Listbox>
</template>

<script>
import { computed, ref } from "vue";
import {
  Listbox,
  ListboxButton,
  ListboxLabel,
  ListboxOption,
  ListboxOptions,
} from "@headlessui/vue";
import { CheckIcon, ChevronDownIcon } from "@heroicons/vue/20/solid";
import ErrorMessage from "../error-message/ErrorMessage.vue";

export default {
  components: {
    ErrorMessage,
    Listbox,
    ListboxButton,
    ListboxLabel,
    ListboxOption,
    ListboxOptions,
    CheckIcon,
    ChevronDownIcon,
  },

  props: {
    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * V-model value.
     *
     * @type {Array|Number|String|Object}
     */
    modelValue: {
      type: [Array, Number, String],
    },

    /**
     * Options.
     *
     * @type {Array|Object}
     */
    options: {
      type: Array,
    },

    /**
     * Option key.
     *
     * @type {String|Object}
     */
    optionKey: {
      type: String,
      default: "key",
    },

    /**
     * Option label.
     *
     * @type {String|Object}
     */
    optionLabel: {
      type: String,
      default: "label",
    },

    /**
     * Placeholder.
     *
     * @type {String|Object}
     */
    placeholder: {
      type: String,
      default: "Select from list",
    },

    /**
     * Multiple.
     *
     * @type {Boolean|Object}
     */
    multiple: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Error.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: () => {
        return null;
      },
    },

    /**
     * Is Required?
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is disabled?
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is optional?
     *
     * @type {Boolean|Object}
     */
    isOptional: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Get options.
     */
    const getOptions = computed(() => {
      return props.options.map((option) => {
        return {
          value: option[props.optionKey],
          label: option[props.optionLabel],
        };
      });
    });

    /**
     * Selected values.
     */
    const selectedValues = computed({
      get: () => {
        if (props.multiple) {
          const value = Array.isArray(props.modelValue)
            ? props.modelValue
            : [props.modelValue];

          return value.map((val) => {
            const opt = getOptions.value.filter((opt) => opt.value === val);

            return opt[0];
          });
        }

        if (props.modelValue || props.modelValue === 0) {
          const opt = getOptions.value.filter(
            (opt) => opt.value === props.modelValue
          );

          return opt[0] ?? null;
        }

        return null;
      },

      set: (item) => {
        if (props.multiple) {
          emit(
            "update:modelValue",
            item.map((i) => i.value)
          );

          return;
        }

        emit("update:modelValue", item.value);
      },
    });

    /**
     * Has error.
     *
     * @return {boolean}
     */
    const hasError = computed(() => {
      return !!props.error;
    });

    /**
     * Get selected values.
     */
    const getSelectedValues = computed(() => {
      const values = selectedValues.value;

      if (props.multiple) {
        if (values.length > 0) return values.map((val) => val.label).join(", ");

        return props.placeholder;
      }

      if (values) {
        return values[props.optionLabel];
      }

      return props.placeholder;
    });

    /**
     * Get error class.
     */
    const getErrorClass = computed(() => {
      return hasError.value
        ? "border-red-500 focus:border-red-600"
        : "border-gray-300 focus:border-gray-400";
    });

    return {
      getSelectedValues,
      getErrorClass,
      selectedValues,
      hasError,
      getOptions,
    };
  },
};
</script>
