<template>
  <div>
    <v-label
      v-if="label"
      :label="label"
      :name="name"
      :is-required="isRequired"
      :is-optional="isOptional"
    />

    <div class="my-1.5 relative rounded-md shadow-sm">
      <input
        :id="name"
        v-model="text"
        :placeholder="placeholder"
        :type="type"
        :maxlength="maxLength"
        :disabled="isDisabled"
        :class="[getErrorClass, getDisabledClass]"
        class="block border text-sm p-2 rounded w-full focus:outline-none"
      />
    </div>

    <error-message v-if="error">{{ error }}</error-message>
  </div>
</template>

<script>
import { computed } from "vue";

import VLabel from "../label/Label.vue";
import ErrorMessage from "../error-message/ErrorMessage.vue";

export default {
  components: {
    ErrorMessage,
    VLabel,
  },

  props: {
    /**
     * Name.
     *
     * @type {String|Object}
     */
    name: {
      type: String,
    },

    /**
     * Label.
     *
     * @type {String|Object}
     */
    label: {
      type: String,
    },

    /**
     * V-model value.
     *
     * @type {String|Number|Object}
     */
    modelValue: {
      type: [String, Number],
    },

    /**
     * Placeholder.
     *
     * @type {String|Object}
     */
    placeholder: {
      type: String,
      default: () => {
        return null;
      },
    },

    /**
     * Error.
     *
     * @type {String|Object}
     */
    error: {
      type: String,
      default: () => {
        return null;
      },
    },

    /**
     * Max length.
     *
     * @type {Number|Object}
     */
    maxLength: {
      type: Number,
      default: 255,
    },

    /**
     * Type of input.
     *
     * @type {String|Object}
     */
    type: {
      type: String,
      default: "text",
      validator: (value) => {
        return ["text", "password", "email", "number"].indexOf(value) !== -1;
      },
    },

    /**
     * Is Required?
     *
     * @type {Boolean|Object}
     */
    isRequired: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is disabled?
     *
     * @type {Boolean|Object}
     */
    isDisabled: {
      type: Boolean,
      default: () => {
        return false;
      },
    },

    /**
     * Is optional?
     *
     * @type {Boolean|Object}
     */
    isOptional: {
      type: Boolean,
      default: () => {
        return false;
      },
    },
  },

  setup(props, { emit }) {
    /**
     * Text.
     */
    const text = computed({
      get: () => props.modelValue,
      set: (value) => emit(`update:modelValue`, value),
    });

    /**
     * Has error.
     *
     * @return {boolean}
     */
    const hasError = computed(() => {
      return !!props.error;
    });

    /**
     * Get error class.
     */
    const getErrorClass = computed(() => {
      return hasError.value
        ? "border-red-500 focus:border-red-600"
        : "border-gray-300 focus:border-gray-400";
    });

    /**
     * Get disabled class.
     */
    const getDisabledClass = computed(() => {
      return props.isDisabled ? "bg-gray-200 text-gray-500" : null;
    });

    return {
      text,
      hasError,
      getErrorClass,
      getDisabledClass,
    };
  },
};
</script>
